import React, { useState, useEffect } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { DocumentIcon, PlusCircleIcon, XIcon } from '../Icons';

interface Note {
    id: number;
    title: string;
    content: string;
}

interface DigitalNotebookProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const DigitalNotebook: React.FC<DigitalNotebookProps> = ({ setActiveTool, breadcrumbs }) => {
    const [notes, setNotes] = useState<Note[]>([
        { id: 1, title: 'Perfect Chocolate Chip Cookie', content: '- Use brown butter\n- Chill dough for 24 hours\n- Sprinkle with flaky sea salt' }
    ]);
    const [activeNoteId, setActiveNoteId] = useState<number | null>(1);

    const activeNote = notes.find(note => note.id === activeNoteId);

    const handleAddNote = () => {
        const newNote: Note = {
            id: Date.now(),
            title: 'New Note',
            content: ''
        };
        setNotes([...notes, newNote]);
        setActiveNoteId(newNote.id);
    };

    const handleDeleteNote = (id: number) => {
        setNotes(notes.filter(note => note.id !== id));
        if (activeNoteId === id) {
            setActiveNoteId(notes.length > 1 ? notes.find(n => n.id !== id)!.id : null);
        }
    };
    
    const handleUpdateNote = (field: 'title' | 'content', value: string) => {
        if (!activeNoteId) return;
        setNotes(notes.map(note => note.id === activeNoteId ? {...note, [field]: value} : note));
    };

    return (
        <ToolContainer 
            title="Free Digital Recipe Notebook" 
            description="Your personal digital notebook and online recipe keeper. A free, private space to save your recipes, make notes, and track your baking experiments." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="flex flex-col md:flex-row gap-4 min-h-[600px]">
                <aside className="w-full md:w-1/4 bg-brand-pink-light/60 p-3 rounded-lg border border-brand-border">
                    <button onClick={handleAddNote} className="w-full flex items-center justify-center gap-2 bg-white text-brand-pink font-bold py-2 px-4 rounded-lg shadow-sm hover:bg-brand-pink-light transition-colors border border-brand-border mb-3">
                        <PlusCircleIcon className="w-5 h-5" /> New Note
                    </button>
                    <ul className="space-y-1">
                        {notes.map(note => (
                            <li key={note.id}>
                                <button onClick={() => setActiveNoteId(note.id)} className={`w-full text-left p-2 rounded-md font-medium text-sm flex justify-between items-center group ${activeNoteId === note.id ? 'bg-brand-pink text-white' : 'text-brand-text-body hover:bg-white'}`}>
                                    <span className="truncate pr-2">{note.title}</span>
                                    <XIcon onClick={(e) => { e.stopPropagation(); handleDeleteNote(note.id); }} className="w-4 h-4 flex-shrink-0 opacity-0 group-hover:opacity-100" />
                                </button>
                            </li>
                        ))}
                    </ul>
                </aside>

                <main className="flex-1">
                    {activeNote ? (
                        <div className="h-full flex flex-col">
                            <input 
                                type="text"
                                value={activeNote.title}
                                onChange={e => handleUpdateNote('title', e.target.value)}
                                className="w-full text-2xl font-bold text-brand-text-title p-2 mb-2 bg-transparent focus:outline-none border-b-2 border-brand-border focus:border-brand-pink transition-colors"
                            />
                            <textarea 
                                value={activeNote.content}
                                onChange={e => handleUpdateNote('content', e.target.value)}
                                placeholder="Start writing your notes here..."
                                className="flex-grow w-full p-2 bg-transparent focus:outline-none resize-none text-brand-text-body leading-relaxed"
                            />
                        </div>
                    ) : (
                        <div className="flex flex-col items-center justify-center h-full text-center text-brand-text-body/70">
                            <DocumentIcon className="w-16 h-16 mb-4" />
                            <h3 className="text-xl font-bold text-brand-text-title">No note selected</h3>
                            <p>Select a note from the list or create a new one.</p>
                        </div>
                    )}
                </main>
            </div>
             <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Getting the Most Out of Your Online Recipe Keeper</h3>
                <ul className="list-disc pl-5 text-brand-text-body/90 leading-relaxed space-y-2">
                    <li><strong>Create a Template:</strong> For new recipes, create a note with standard headings like "Source," "Ingredients," "Instructions," "Results," and "Changes for Next Time." This keeps your digital notebook consistent.</li>
                    <li><strong>Record Everything:</strong> Did you bake on a humid day? Was your oven running hot? Note down these environmental factors. They can have a huge impact on your results.</li>
                    <li><strong>Review and Refine:</strong> Before you bake a recipe again, quickly review your notes from last time. This is the fastest way to learn from past mistakes and perfect your technique.</li>
                </ul>
            </div>
        </ToolContainer>
    );
};
