import React, { useState, useEffect } from 'react';
import { ToolContainer } from './ToolContainer';
import { WaterDropIcon } from '../Icons';
import type { ToolId } from '../utils/slugs';

interface HydrationConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const HydrationConverter: React.FC<HydrationConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [flour1, setFlour1] = useState('all-purpose');
    const [flour2, setFlour2] = useState('bread-flour');
    const [originalLiquid, setOriginalLiquid] = useState(100);
    const [adjustedLiquid, setAdjustedLiquid] = useState(0);

    const absorptionRates: { [key: string]: number } = {
        'all-purpose': 0.62,
        'bread-flour': 0.65,
        'whole-wheat': 0.70,
        'rye': 0.75,
        'cake-flour': 0.58,
    };

    useEffect(() => {
        const rate1 = absorptionRates[flour1];
        const rate2 = absorptionRates[flour2];

        if (rate1 && rate2) {
            const waterInOriginalFlour = originalLiquid * rate1;
            const newLiquidAmount = waterInOriginalFlour / rate2;
            setAdjustedLiquid(parseFloat(newLiquidAmount.toFixed(2)));
        }
    }, [flour1, flour2, originalLiquid]);

    return (
        <ToolContainer
            title="Free Flour Hydration Converter"
            description="Adjust the liquid in your recipe for free when substituting flours with different absorption rates, like swapping bread flour for whole wheat."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-800 mb-4">Original Recipe</h3>
                    <div className="space-y-4">
                        <div>
                            <label htmlFor="flour1" className="block text-sm font-medium text-gray-700">Original Flour Type</label>
                            <select id="flour1" value={flour1} onChange={e => setFlour1(e.target.value)} className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-pink-500 focus:border-pink-500">
                                <option value="all-purpose">All-Purpose Flour</option>
                                <option value="bread-flour">Bread Flour</option>
                                <option value="whole-wheat">Whole Wheat Flour</option>
                                <option value="rye">Rye Flour</option>
                                <option value="cake-flour">Cake Flour</option>
                            </select>
                        </div>
                        <div>
                            <label htmlFor="originalLiquid" className="block text-sm font-medium text-gray-700">Original Liquid Amount (grams)</label>
                            <input type="number" id="originalLiquid" value={originalLiquid} onChange={e => setOriginalLiquid(parseFloat(e.target.value))} className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-pink-500 focus:border-pink-500" />
                        </div>
                    </div>
                </div>

                <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                    <h3 className="text-lg font-semibold text-gray-800 mb-4">Adjusted Recipe</h3>
                    <div className="space-y-4">
                        <div>
                            <label htmlFor="flour2" className="block text-sm font-medium text-gray-700">New Flour Type</label>
                            <select id="flour2" value={flour2} onChange={e => setFlour2(e.target.value)} className="mt-1 block w-full p-2 border border-gray-300 rounded-md shadow-sm focus:ring-pink-500 focus:border-pink-500">
                                <option value="all-purpose">All-Purpose Flour</option>
                                <option value="bread-flour">Bread Flour</option>
                                <option value="whole-wheat">Whole Wheat Flour</option>
                                <option value="rye">Rye Flour</option>
                                <option value="cake-flour">Cake Flour</option>
                            </select>
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700">Adjusted Liquid Amount (grams)</label>
                            <div className="mt-1 p-2 bg-gray-100 rounded-md text-lg font-bold text-pink-600">{adjustedLiquid}g</div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Why Flour Hydration Matters</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>
                        Not all flours absorb water equally. Whole wheat and rye flours, for example, are much "thirstier" than all-purpose or cake flour because they contain more bran and germ, which soak up liquid.
                    </p>
                    <p>
                        If you substitute a thirsty flour (like whole wheat) for a less absorbent one (like all-purpose) without adjusting the liquid, your dough will be stiff and dry. Conversely, if you swap in a less absorbent flour, your dough may become too wet and sticky to handle.
                    </p>
                    <p>This calculator helps you maintain the intended hydration and consistency of your original recipe by adjusting the amount of liquid based on the typical absorption rates of different flours.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
