import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
// Fix: Corrected import path for ToolId
import type { ToolId } from '../utils/slugs';
import { ClipboardIcon, CheckCircleIcon } from '../Icons';

const YEAST_RATIOS = {
  // Ratio to Active Dry
  'active-dry': 1,
  'instant': 0.75,
  'fresh': 2.5,
};

const YEAST_NAMES: Record<string, string> = {
    'active-dry': 'Active Dry Yeast',
    'instant': 'Instant Yeast',
    'fresh': 'Fresh Yeast'
};

interface YeastConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const YeastConverter: React.FC<YeastConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [amount, setAmount] = useState('1');
    const [fromYeast, setFromYeast] = useState('active-dry');
    const [copied, setCopied] = useState(false);

    const results = useMemo(() => {
        const inputAmount = parseFloat(amount) || 0;
        const fromRatio = YEAST_RATIOS[fromYeast as keyof typeof YEAST_RATIOS];
        
        // Convert input to an "active dry" equivalent
        const activeDryEquivalent = inputAmount / fromRatio;

        return {
            'active-dry': (activeDryEquivalent * YEAST_RATIOS['active-dry']).toFixed(2),
            'instant': (activeDryEquivalent * YEAST_RATIOS['instant']).toFixed(2),
            'fresh': (activeDryEquivalent * YEAST_RATIOS['fresh']).toFixed(2),
        };

    }, [amount, fromYeast]);

     const handleCopy = async () => {
        const text = `Active Dry Yeast: ${results['active-dry']} tsp\nInstant Yeast: ${results['instant']} tsp\nFresh Yeast: ${results['fresh']} tsp`;
        try {
            await navigator.clipboard.writeText(text);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy results.');
        }
    };

    return (
        <ToolContainer
            title="Free Yeast Conversion Calculator"
            description="Instantly convert between active dry, instant, and fresh yeast. Get the correct yeast substitution for any bread recipe, including active dry to instant yeast conversion."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-2 gap-8 items-start">
                <div className="space-y-4">
                    <div>
                        <label className="block text-sm font-bold text-brand-text-title mb-2">Amount (in teaspoons)</label>
                        <input
                            type="number"
                            value={amount}
                            onChange={e => setAmount(e.target.value)}
                            className="w-full px-4 py-3 bg-white border-2 border-brand-border rounded-lg text-2xl font-bold"
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-bold text-brand-text-title mb-2">Original Yeast Type</label>
                        <select
                            value={fromYeast}
                            onChange={e => setFromYeast(e.target.value)}
                            className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg"
                        >
                            {Object.keys(YEAST_NAMES).map(key => (
                                <option key={key} value={key}>{YEAST_NAMES[key]}</option>
                            ))}
                        </select>
                    </div>
                </div>

                <div className="bg-brand-pink-light p-6 rounded-lg sticky top-6">
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-4 text-center">Equivalent Amounts</h3>
                    <ul className="space-y-3">
                        {Object.keys(results).map(key => (
                            <li key={key} className={`flex justify-between items-center p-3 rounded-md ${key === fromYeast ? 'bg-yellow-100 border-yellow-300 border-2' : 'bg-white border-brand-border border'}`}>
                                <span className="font-semibold text-brand-text-body">{YEAST_NAMES[key]}</span>
                                <span className="font-bold text-brand-text-title">{results[key as keyof typeof results]} tsp</span>
                            </li>
                        ))}
                    </ul>
                    <div className="mt-4 text-center">
                        <button onClick={handleCopy} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                            {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                            {copied ? 'Results Copied!' : 'Copy All Results'}
                        </button>
                    </div>
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Understanding Yeast Conversions</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>Different types of yeast have different potencies and require different amounts. This active dry to instant yeast conversion (and fresh yeast) calculator uses standard industry ratios:</p>
                    <ul className="list-disc pl-5">
                        <li><strong>Active Dry Yeast:</strong> The traditional choice. It usually needs to be "bloomed" in warm water before use. This is our baseline (1:1).</li>
                        <li><strong>Instant Yeast (or Rapid-Rise):</strong> More potent than active dry. You generally need about 25% less. It can often be mixed directly with dry ingredients. Ratio is 1 part active dry to 0.75 parts instant.</li>
                        <li><strong>Fresh Yeast (or Cake Yeast):</strong> A block of live, compressed yeast cells. It's much less potent by weight and requires refrigeration. You'll need significantly more. Ratio is 1 part active dry to 2.5 parts fresh.</li>
                    </ul>
                    <p>Always check your recipe's instructions, as some may have specific requirements. These conversions are based on weight/potency, not volume, but are commonly approximated with teaspoons for home baking.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
