import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { ClipboardIcon, CheckCircleIcon } from '../Icons';

const CONVERSIONS: Record<string, Record<string, number>> = {
  // Grams per unit
  'all-purpose-flour': { cups: 120, tbsp: 7.5, tsp: 2.5, oz: 4.25 },
  'bread-flour': { cups: 127, tbsp: 8, tsp: 2.7, oz: 4.5 },
  'cake-flour': { cups: 114, tbsp: 7.1, tsp: 2.4, oz: 4.0 },
  'granulated-sugar': { cups: 200, tbsp: 12.5, tsp: 4.2, oz: 7.1 },
  'powdered-sugar': { cups: 120, tbsp: 7.5, tsp: 2.5, oz: 4.25 },
  'brown-sugar': { cups: 220, tbsp: 13.75, tsp: 4.6, oz: 7.75 },
  'cocoa-powder': { cups: 85, tbsp: 5.3, tsp: 1.8, oz: 3.0 },
  'cornstarch': { cups: 128, tbsp: 8, tsp: 2.7, oz: 4.5 },
  'butter': { cups: 227, tbsp: 14.2, tsp: 4.7, oz: 8 },
  'oil': { cups: 218, tbsp: 13.6, tsp: 4.5, oz: 7.7 },
  'water-milk': { cups: 240, tbsp: 15, tsp: 5, oz: 8.45 },
  'rolled-oats': { cups: 85, tbsp: 5.3, tsp: 1.8, oz: 3.0 },
};

const UNITS = {
    volume: ['cups', 'tbsp', 'tsp'],
    weight: ['g', 'oz']
}

interface MeasurementConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const MeasurementConverter: React.FC<MeasurementConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [amount, setAmount] = useState('1');
    const [fromUnit, setFromUnit] = useState('cups');
    const [toUnit, setToUnit] = useState('g');
    const [ingredient, setIngredient] = useState('all-purpose-flour');
    const [copied, setCopied] = useState(false);
    
    const result = useMemo(() => {
        const value = parseFloat(amount) || 0;
        if(value === 0) return 0;
        
        const ingredientData = CONVERSIONS[ingredient];
        if (!ingredientData) return 0;
        
        // Convert input to grams
        let grams;
        if (fromUnit === 'g') {
            grams = value;
        } else if (fromUnit === 'oz') {
            grams = value * 28.35;
        } else { // Volume to weight
            const gPerCup = ingredientData.cups;
            const gPerTbsp = ingredientData.tbsp;
            const gPerTsp = ingredientData.tsp;
            if (fromUnit === 'cups') grams = value * gPerCup;
            else if (fromUnit === 'tbsp') grams = value * gPerTbsp;
            else if (fromUnit === 'tsp') grams = value * gPerTsp;
            else grams = 0;
        }

        // Convert grams to output unit
        let output;
        if (toUnit === 'g') {
            output = grams;
        } else if (toUnit === 'oz') {
            output = grams / 28.35;
        } else { // Weight to volume
            const gPerCup = ingredientData.cups;
            const gPerTbsp = ingredientData.tbsp;
            const gPerTsp = ingredientData.tsp;
            if (toUnit === 'cups') output = grams / gPerCup;
            else if (toUnit === 'tbsp') output = grams / gPerTbsp;
            else if (toUnit === 'tsp') output = grams / gPerTsp;
            else output = 0;
        }
        
        return parseFloat(output.toFixed(2));
    }, [amount, fromUnit, toUnit, ingredient]);

    const handleCopy = async () => {
        try {
            await navigator.clipboard.writeText(result.toString());
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy result.');
        }
    };
    
    return (
        <ToolContainer 
            title="Baking Measurement Converter (Cups to Grams)" 
            description="The most accurate baking converter for volume to weight measurements. Use our baking conversion chart to convert cups to grams for flour, sugar, butter, and more." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-3 gap-4 items-end">
                 <div>
                    <label className="block text-sm font-medium mb-1">Amount</label>
                    <input type="number" value={amount} onChange={e => setAmount(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                </div>
                <div>
                    <label className="block text-sm font-medium mb-1">From Unit</label>
                    <select value={fromUnit} onChange={e => setFromUnit(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg">
                        <optgroup label="Volume">
                            {UNITS.volume.map(u => <option key={u} value={u}>{u}</option>)}
                        </optgroup>
                        <optgroup label="Weight">
                            {UNITS.weight.map(u => <option key={u} value={u}>{u}</option>)}
                        </optgroup>
                    </select>
                </div>
                 <div>
                    <label className="block text-sm font-medium mb-1">To Unit</label>
                    <select value={toUnit} onChange={e => setToUnit(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg">
                        <optgroup label="Weight">
                            {UNITS.weight.map(u => <option key={u} value={u}>{u}</option>)}
                        </optgroup>
                        <optgroup label="Volume">
                            {UNITS.volume.map(u => <option key={u} value={u}>{u}</option>)}
                        </optgroup>
                    </select>
                </div>
            </div>
             <div className="mt-4">
                <label className="block text-sm font-medium mb-1">Ingredient</label>
                <select value={ingredient} onChange={e => setIngredient(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg">
                    <option value="all-purpose-flour">All-Purpose Flour</option>
                    <option value="bread-flour">Bread Flour</option>
                    <option value="cake-flour">Cake Flour</option>
                    <option value="granulated-sugar">Granulated Sugar</option>
                    <option value="powdered-sugar">Powdered Sugar</option>
                    <option value="brown-sugar">Brown Sugar (packed)</option>
                    <option value="cocoa-powder">Cocoa Powder</option>
                    <option value="cornstarch">Cornstarch</option>
                    <option value="butter">Butter</option>
                    <option value="oil">Oil</option>
                    <option value="water-milk">Water / Milk</option>
                    <option value="rolled-oats">Rolled Oats</option>
                </select>
            </div>
            <div className="mt-8 text-center bg-brand-pink-light p-6 rounded-lg">
                 <p className="text-lg text-brand-text-body">Result:</p>
                 <div className="flex items-center justify-center gap-2">
                    <p className="text-4xl font-bold text-brand-pink">{amount} {fromUnit} = {result.toLocaleString()} {toUnit}</p>
                    <button onClick={handleCopy} title="Copy result" className="p-1">
                        {copied ? <CheckCircleIcon className="w-5 h-5 text-green-500" /> : <ClipboardIcon className="w-5 h-5 text-brand-text-body/50 hover:text-brand-pink" />}
                    </button>
                 </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border text-center text-sm text-brand-text-body/80">
                <p><strong>Why convert from cups to grams?</strong> Baking by weight is significantly more accurate than baking by volume. A "cup of flour" can vary in weight depending on how it's packed, but 120 grams is always 120 grams. For consistent, professional-quality results, a kitchen scale and a reliable baking converter are a baker's best friends.</p>
            </div>
        </ToolContainer>
    );
};
