import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { PaletteIcon } from '../Icons';

interface FrostingColorMixerProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

interface ColorRecipe {
  name: string;
  hex: string;
  // Fix: Add optional `isTouch` property to allow for special recipe notes.
  recipe: { color: string; parts: number; isTouch?: boolean }[];
}

interface ColorCategory {
  category: string;
  colors: ColorRecipe[];
}

const colorData: ColorCategory[] = [
    {
        category: 'Pinks & Reds',
        colors: [
            { name: 'Dusty Rose', hex: '#DCAE96', recipe: [{ color: 'Pink', parts: 3 }, { color: 'Brown', parts: 1 }] },
            { name: 'Coral', hex: '#FF7F50', recipe: [{ color: 'Pink', parts: 2 }, { color: 'Yellow', parts: 1 }] },
            { name: 'Burgundy', hex: '#800020', recipe: [{ color: 'Red', parts: 5 }, { color: 'Purple', parts: 1 }] },
            { name: 'Magenta', hex: '#FF00FF', recipe: [{ color: 'Pink', parts: 2 }, { color: 'Purple', parts: 1 }] },
            { name: 'Peach', hex: '#FFDAB9', recipe: [{ color: 'Orange', parts: 1 }, { color: 'Pink', parts: 1 }] },
        ]
    },
    {
        category: 'Oranges & Yellows',
        colors: [
            { name: 'Marigold', hex: '#EAA221', recipe: [{ color: 'Yellow', parts: 2 }, { color: 'Orange', parts: 1 }] },
            { name: 'Gold', hex: '#FFD700', recipe: [{ color: 'Yellow', parts: 10 }, { color: 'Orange', parts: 3 }, { color: 'Red', parts: 1 }] },
            { name: 'Apricot', hex: '#FBCEB1', recipe: [{ color: 'Orange', parts: 2 }, { color: 'Gold', parts: 1 }] },
        ]
    },
    {
        category: 'Greens',
        colors: [
            { name: 'Mint Green', hex: '#98FF98', recipe: [{ color: 'Green', parts: 2 }, { color: 'Yellow', parts: 1 }] },
            { name: 'Sage Green', hex: '#B2AC88', recipe: [{ color: 'Leaf Green', parts: 3 }, { color: 'Brown', parts: 1 }] },
            { name: 'Olive Green', hex: '#808000', recipe: [{ color: 'Leaf Green', parts: 3 }, { color: 'Yellow', parts: 1 }, { color: 'Black', parts: 1, isTouch: true }] },
            { name: 'Teal', hex: '#008080', recipe: [{ color: 'Blue', parts: 2 }, { color: 'Green', parts: 1 }] },
        ]
    },
    {
        category: 'Blues & Purples',
        colors: [
            { name: 'Lavender', hex: '#E6E6FA', recipe: [{ color: 'Pink', parts: 3 }, { color: 'Blue', parts: 1 }] },
            { name: 'Periwinkle', hex: '#CCCCFF', recipe: [{ color: 'Royal Blue', parts: 1 }, { color: 'Violet', parts: 1 }] },
            { name: 'Navy Blue', hex: '#000080', recipe: [{ color: 'Royal Blue', parts: 4 }, { color: 'Black', parts: 1 }] },
            { name: 'Aqua', hex: '#00FFFF', recipe: [{ color: 'Sky Blue', parts: 2 }, { color: 'Leaf Green', parts: 1 }] },
        ]
    },
    {
        category: 'Browns & Neutrals',
        colors: [
            { name: 'Ivory', hex: '#FFFFF0', recipe: [{ color: 'Brown', parts: 1, isTouch: true }] },
            { name: 'Light Gray', hex: '#D3D3D3', recipe: [{ color: 'Black', parts: 1, isTouch: true }] },
            { name: 'Charcoal', hex: '#36454F', recipe: [{ color: 'Black', parts: 2 }, { color: 'Royal Blue', parts: 1, isTouch: true }] },
            { name: 'Taupe', hex: '#483C32', recipe: [{ color: 'Brown', parts: 2 }, { color: 'Black', parts: 1 }] },
        ]
    }
];

export const FrostingColorMixer: React.FC<FrostingColorMixerProps> = ({ setActiveTool, breadcrumbs }) => {
    const [selectedColor, setSelectedColor] = useState<ColorRecipe | null>(colorData[0].colors[0]);

    return (
        <ToolContainer
            title="Free Frosting Color Mixer Chart"
            description="Create custom buttercream and royal icing colors with our free food coloring mixing chart. It provides color drop ratios to achieve the perfect shade."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid lg:grid-cols-3 gap-8 items-start">
                <div className="lg:col-span-2 space-y-6">
                    {colorData.map(category => (
                        <div key={category.category}>
                            <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-3">{category.category}</h3>
                            <div className="grid grid-cols-3 sm:grid-cols-4 md:grid-cols-5 gap-3">
                                {category.colors.map(color => (
                                    <button
                                        key={color.name}
                                        onClick={() => setSelectedColor(color)}
                                        className={`p-2 rounded-lg border-2 transition-all duration-200 ${selectedColor?.name === color.name ? 'border-brand-pink scale-110 shadow-lg' : 'border-transparent hover:scale-105'}`}
                                        aria-label={`Select color: ${color.name}`}
                                    >
                                        <div style={{ backgroundColor: color.hex }} className="w-full aspect-square rounded-md shadow-inner border border-black/10"></div>
                                        <p className="text-xs font-semibold text-brand-text-body mt-1.5 truncate">{color.name}</p>
                                    </button>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>

                <div className="sticky top-6">
                    {selectedColor ? (
                        <div className="bg-brand-pink-light p-6 rounded-2xl border border-brand-border shadow-sm fade-in">
                            <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-4 text-center">Color Recipe</h3>
                            <div className="flex items-center gap-4 mb-4">
                                <div style={{ backgroundColor: selectedColor.hex }} className="w-20 h-20 rounded-xl shadow-md border-2 border-white flex-shrink-0"></div>
                                <p className="font-bold text-2xl text-brand-pink">{selectedColor.name}</p>
                            </div>
                            <div className="bg-white p-4 rounded-lg border border-brand-pink/20">
                                <p className="font-semibold text-brand-text-body mb-2">Mixing Ratio:</p>
                                <p className="text-xl font-bold text-brand-text-title">
                                    {selectedColor.recipe.map((r, i) => (
                                        <span key={i}>
                                            {i > 0 && ' + '}
                                            {r.parts} part{r.parts > 1 ? 's' : ''} {r.color}
                                            {r.isTouch && ' (just a touch!)'}
                                        </span>
                                    ))}
                                </p>
                            </div>
                            <p className="text-xs text-brand-text-body/80 mt-4 text-center">
                                Ratios are a starting point for ~1 cup of white frosting. Use a toothpick to add color gradually.
                            </p>
                        </div>
                    ) : (
                        <div className="bg-brand-pink-light/60 p-6 rounded-2xl text-center flex flex-col items-center justify-center h-64">
                            <PaletteIcon className="w-12 h-12 text-brand-pink mb-4" />
                            <h3 className="text-xl font-bold text-brand-text-title">Select a color</h3>
                            <p className="text-sm text-brand-text-body/80 mt-1">The mixing formula will appear here.</p>
                        </div>
                    )}
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Tips for Perfect Frosting Colors</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <ul>
                        <li><strong>Start Small:</strong> It's easier to add color than to remove it. Use a toothpick to add tiny amounts of gel food coloring at a time, especially for dark colors like black or red.</li>
                        <li><strong>Let it Deepen:</strong> Colors, especially reds, blues, and blacks, will darken or "develop" over time. For the most accurate shade, mix your color a few hours ahead of time (or even the day before) and store it in an airtight container.</li>
                        <li><strong>Mute Your Colors:</strong> To get dusty or muted tones (like sage green or dusty rose), add a tiny touch of the color's opposite on the color wheel (e.g., a tiny bit of red to mute green). A small amount of brown or black also works well.</li>
                        <li><strong>Beware the Base:</strong> These ratios assume a pure white base, like American buttercream made with shortening or Swiss meringue buttercream. If your frosting is yellowish from butter, it will affect the final color (e.g., adding blue will result in a greenish hue).</li>
                    </ul>
                </div>
            </div>
        </ToolContainer>
    );
};
