import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { ClipboardIcon, CheckCircleIcon, ButterIcon } from '../Icons';

interface ButterfatConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const ButterfatConverter: React.FC<ButterfatConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [originalAmount, setOriginalAmount] = useState('227'); // 1 cup of butter in grams
    const [originalFat, setOriginalFat] = useState('80'); // Standard American butter
    const [targetFat, setTargetFat] = useState('85'); // Standard European butter
    const [copied, setCopied] = useState(false);

    const { newButterAmount, waterAdjustment } = useMemo(() => {
        const amount = parseFloat(originalAmount) || 0;
        const ogFat = parseFloat(originalFat) / 100 || 0;
        const tFat = parseFloat(targetFat) / 100 || 0;

        if (amount === 0 || ogFat === 0 || tFat === 0) {
            return { newButterAmount: 0, waterAdjustment: 0 };
        }

        const originalFatWeight = amount * ogFat;
        const newAmount = originalFatWeight / tFat;
        const waterAdj = (amount * (1 - ogFat)) - (newAmount * (1 - tFat));

        return {
            newButterAmount: parseFloat(newAmount.toFixed(1)),
            waterAdjustment: parseFloat(waterAdj.toFixed(1)),
        };
    }, [originalAmount, originalFat, targetFat]);

    const handleCopy = async () => {
        const textToCopy = `New Butter Amount: ${newButterAmount}g\nWater Adjustment: ${waterAdjustment}g`;
        try {
            await navigator.clipboard.writeText(textToCopy);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy result.');
        }
    };

    return (
        <ToolContainer
            title="Free Butterfat Converter"
            description="Adjust your recipe for free when switching between butters with different fat percentages (e.g., American vs. European butter)."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-2 gap-8 items-start">
                <div className="space-y-4">
                    <div>
                        <label className="block text-sm font-bold text-brand-text-title mb-2">Original Butter</label>
                        <div className="grid grid-cols-2 gap-2">
                            <input type="number" value={originalAmount} onChange={e => setOriginalAmount(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" placeholder="Amount (g)" />
                            <input type="number" value={originalFat} onChange={e => setOriginalFat(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" placeholder="Fat %" />
                        </div>
                    </div>
                    <div>
                        <label className="block text-sm font-bold text-brand-text-title mb-2">New Butter</label>
                        <input type="number" value={targetFat} onChange={e => setTargetFat(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" placeholder="Fat %" />
                    </div>
                </div>

                <div className="bg-brand-pink-light p-6 rounded-lg text-center sticky top-6">
                    <ButterIcon className="w-12 h-12 mx-auto text-brand-pink mb-2" />
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-2">Recipe Adjustments</h3>
                    <div className="space-y-3">
                        <div className="bg-white p-3 rounded-lg border">
                            <p className="text-sm font-semibold text-brand-text-body">New Butter Amount</p>
                            <p className="font-bold text-3xl text-brand-pink">{newButterAmount}g</p>
                        </div>
                        <div className="bg-white p-3 rounded-lg border">
                            <p className="text-sm font-semibold text-brand-text-body">
                                {waterAdjustment >= 0 ? 'Add Water/Liquid' : 'Reduce Water/Liquid'}
                            </p>
                            <p className="font-bold text-3xl text-brand-pink">{Math.abs(waterAdjustment)}g</p>
                        </div>
                    </div>
                    <div className="mt-4">
                        <button onClick={handleCopy} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                            {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                            {copied ? 'Adjustments Copied!' : 'Copy Adjustments'}
                        </button>
                    </div>
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Why Butterfat Percentage Matters</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>Not all butters are created equal! The main difference between brands, especially American-style vs. European-style butter, is the butterfat content. The remaining percentage is mostly water.</p>
                    <ul className="list-disc pl-5">
                        <li><strong>American Butter:</strong> Typically contains around <strong>80% butterfat</strong>.</li>
                        <li><strong>European Butter:</strong> Often cultured and contains a higher butterfat percentage, usually <strong>82-86%</strong>.</li>
                    </ul>
                    <p>This difference in water content can significantly affect a recipe's hydration. A higher-fat butter introduces less water, which can lead to flakier pastries (like croissants and pie crusts) and richer-tasting cookies and cakes. When you substitute one for another without adjusting, you might end up with a dough that's too wet or too dry.</p>
                    <p>This calculator helps you keep the total fat and water content of your recipe consistent. It tells you how much of the new butter to use to match the original amount of fat, and then how much liquid to add or remove from your recipe to match the original water content.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
