import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { MortgageIcon, InfoIcon } from '../Icons';

interface MortgagePayoffCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const InputField: React.FC<{ label: string, id: string, value: string, onChange: (val: string) => void, prefix?: string, suffix?: string }> = ({ label, id, value, onChange, prefix, suffix }) => (
    <div>
        <label htmlFor={id} className="block text-sm font-bold text-brand-text-title mb-1">{label}</label>
        <div className="relative">
            {prefix && <span className="absolute left-3 top-1/2 -translate-y-1/2 text-brand-text-body/60">{prefix}</span>}
            <input
                id={id}
                type="number"
                value={value}
                onChange={e => onChange(e.target.value)}
                className={`w-full px-4 py-2.5 bg-white border border-brand-border rounded-lg ${prefix ? 'pl-7' : ''} ${suffix ? 'pr-10' : ''}`}
            />
            {suffix && <span className="absolute right-3 top-1/2 -translate-y-1/2 text-brand-text-body/60">{suffix}</span>}
        </div>
    </div>
);

const ResultCard: React.FC<{ title: string, value: React.ReactNode, subtext?: string }> = ({ title, value, subtext }) => (
    <div className="bg-white p-4 rounded-lg border text-center">
        <p className="text-sm font-semibold text-brand-text-body">{title}</p>
        <p className="font-bold text-3xl text-brand-pink my-1">{value}</p>
        {subtext && <p className="text-xs text-brand-text-body/70">{subtext}</p>}
    </div>
);

export const MortgagePayoffCalculator: React.FC<MortgagePayoffCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
    const [loanAmount, setLoanAmount] = useState('300000');
    const [interestRate, setInterestRate] = useState('6.5');
    const [loanTerm, setLoanTerm] = useState('30');
    const [extraPayment, setExtraPayment] = useState('200');

    const results = useMemo(() => {
        const P = parseFloat(loanAmount) || 0;
        const r = (parseFloat(interestRate) || 0) / 100 / 12;
        const n = (parseInt(loanTerm) || 0) * 12;
        const extra = parseFloat(extraPayment) || 0;

        if (P <= 0 || r <= 0 || n <= 0) {
            return { timeSaved: '0 years, 0 months', interestSaved: 0, newPayoffDate: 'N/A' };
        }

        // Original mortgage calculation
        const originalMonthlyPayment = P * (r * Math.pow(1 + r, n)) / (Math.pow(1 + r, n) - 1);
        const totalOriginalInterest = (originalMonthlyPayment * n) - P;

        // New mortgage calculation with extra payment
        const newMonthlyPayment = originalMonthlyPayment + extra;
        const newN = - (Math.log(1 - (P * r / newMonthlyPayment)) / Math.log(1 + r));
        const newNumberOfPayments = Math.ceil(newN);

        const totalNewPayment = newMonthlyPayment * newNumberOfPayments;
        const totalNewInterest = totalNewPayment - P;

        const interestSaved = totalOriginalInterest - totalNewInterest;

        const monthsSaved = n - newNumberOfPayments;
        const yearsSaved = Math.floor(monthsSaved / 12);
        const remainingMonthsSaved = monthsSaved % 12;

        const today = new Date();
        const newPayoffDate = new Date(today.setMonth(today.getMonth() + newNumberOfPayments));

        return {
            timeSaved: `${yearsSaved} years, ${remainingMonthsSaved} months`,
            interestSaved: interestSaved,
            newPayoffDate: newPayoffDate.toLocaleDateString('en-US', { year: 'numeric', month: 'long' }),
        };

    }, [loanAmount, interestRate, loanTerm, extraPayment]);

    return (
        <ToolContainer
            title="Mortgage Payoff Calculator"
            description="See how much time and money you can save by making extra monthly payments on your mortgage. A powerful tool for financial planning and achieving debt freedom sooner."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-2 gap-8 items-start">
                <div className="space-y-4">
                    <InputField label="Original Loan Amount" id="loanAmount" value={loanAmount} onChange={setLoanAmount} prefix="$" />
                    <div className="grid grid-cols-2 gap-4">
                        <InputField label="Annual Interest Rate" id="interestRate" value={interestRate} onChange={setInterestRate} suffix="%" />
                        <InputField label="Loan Term" id="loanTerm" value={loanTerm} onChange={setLoanTerm} suffix="yrs" />
                    </div>
                    <InputField label="Extra Monthly Payment" id="extraPayment" value={extraPayment} onChange={setExtraPayment} prefix="$" />
                </div>

                <div className="bg-brand-pink-light p-6 rounded-2xl text-center sticky top-6">
                    <MortgageIcon className="w-12 h-12 mx-auto text-brand-pink mb-2"/>
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-4">Your Payoff Summary</h3>
                    <div className="space-y-3">
                        <ResultCard title="Time Saved" value={results.timeSaved} />
                        <ResultCard title="Total Interest Saved" value={`$${results.interestSaved.toLocaleString(undefined, {maximumFractionDigits: 0})}`} />
                        <ResultCard title="New Payoff Date" value={results.newPayoffDate} />
                    </div>
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">The Power of Extra Payments</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>Making even small extra payments on your mortgage can have a huge impact over the life of the loan. Here's why:</p>
                    <ul className="list-disc pl-5 space-y-2">
                        <li>
                            <strong>Principal Reduction:</strong> Every dollar you pay over your required monthly payment goes directly toward reducing your principal loan balance.
                        </li>
                        <li>
                            <strong>Interest Savings:</strong> Because interest is calculated on your outstanding balance, reducing the principal means you pay less interest over time. As this calculator shows, this can add up to tens of thousands of dollars in savings.
                        </li>
                        <li>
                            <strong>Build Equity Faster:</strong> Paying down your principal faster means you build equity in your home more quickly, which increases your net worth.
                        </li>
                    </ul>
                    <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
                        <p className="flex items-start gap-2"><InfoIcon className="w-5 h-5 text-blue-500 flex-shrink-0 mt-0.5"/><span><strong>Disclaimer:</strong> This calculator is for informational purposes only. Before making extra payments, confirm with your lender that your extra funds will be applied directly to the principal and that there are no prepayment penalties.</span></p>
                    </div>
                </div>
            </div>
        </ToolContainer>
    );
};
