import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { ClipboardIcon, CheckCircleIcon } from '../Icons';
import { toolIdToSlug } from '../utils/slugs';

interface Ingredient {
    id: number;
    name: string;
    quantity: string;
    unit: string;
    weight: string;
}

interface RecipeScalerProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const RecipeScaler: React.FC<RecipeScalerProps> = ({ setActiveTool, breadcrumbs }) => {
    const [ingredients, setIngredients] = useState<Ingredient[]>([{ id: 1, name: 'Flour', quantity: '2', unit: 'cups', weight: '240' }]);
    const [scaleFactor, setScaleFactor] = useState('2');
    const [copied, setCopied] = useState(false);
    
    const handleIngredientChange = (id: number, field: keyof Omit<Ingredient, 'id'>, value: string) => {
        setIngredients(ingredients.map(ing => ing.id === id ? { ...ing, [field]: value } : ing));
    };

    const addIngredient = () => {
        setIngredients([...ingredients, { id: Date.now(), name: '', quantity: '', unit: '', weight: '' }]);
    };
    
    const removeIngredient = (id: number) => {
        setIngredients(ingredients.filter(ing => ing.id !== id));
    };

    const handleCopy = async () => {
        const factor = parseFloat(scaleFactor) || 1;
        const recipeText = ingredients.map(ing => {
            const originalQty = parseFloat(ing.quantity) || 0;
            const originalWeight = parseFloat(ing.weight) || 0;
            
            const newQty = (originalQty * factor).toLocaleString(undefined, { maximumFractionDigits: 2 });
            const newWeight = (originalWeight * factor).toLocaleString(undefined, { maximumFractionDigits: 2 });

            let resultText = '';
            if (originalQty > 0 && ing.unit) {
                resultText += `${newQty} ${ing.unit}`;
            }
            if (originalWeight > 0) {
                if (resultText) resultText += ' / ';
                resultText += `${newWeight} g`;
            }
            return `${ing.name}: ${resultText}`;
        }).join('\n');

        try {
            await navigator.clipboard.writeText(recipeText);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy results.');
        }
    };

    return (
        <ToolContainer 
            title="Free Recipe Scaler & Converter" 
            description="Easily scale any baking recipe up or down with our free recipe scaler. Our recipe multiplier calculator instantly changes ingredient quantities for any batch size." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-2 gap-8">
                <div>
                    <h3 className="font-bold text-lg mb-4 text-brand-text-title">Original Recipe</h3>
                    <div className="space-y-3 mb-4">
                        {ingredients.map((ing, index) => (
                            <div key={ing.id} className="grid grid-cols-1 sm:grid-cols-4 gap-2 items-center">
                                <input type="text" placeholder={`Ingredient ${index + 1}`} value={ing.name} onChange={e => handleIngredientChange(ing.id, 'name', e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                                <input type="number" placeholder="Quantity" value={ing.quantity} onChange={e => handleIngredientChange(ing.id, 'quantity', e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                                <input type="text" placeholder="Unit" value={ing.unit} onChange={e => handleIngredientChange(ing.id, 'unit', e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                                <div className="flex items-center gap-2">
                                    <input type="number" placeholder="Weight (g)" value={ing.weight} onChange={e => handleIngredientChange(ing.id, 'weight', e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                                    <button onClick={() => removeIngredient(ing.id)} className="text-red-500 hover:text-red-700 p-1">&times;</button>
                                </div>
                            </div>
                        ))}
                    </div>
                    <button onClick={addIngredient} className="text-sm font-bold text-brand-pink hover:underline">+ Add Ingredient</button>

                    <div className="mt-8">
                        <label htmlFor="scaleFactor" className="block text-sm font-medium mb-1">Recipe Multiplier (e.g., 0.5 for half, 2 for double)</label>
                        <input id="scaleFactor" type="number" value={scaleFactor} onChange={e => setScaleFactor(e.target.value)} className="w-full px-3 py-2 bg-white border border-brand-border rounded-lg" />
                    </div>
                </div>
                <div className="bg-brand-pink-light p-6 rounded-lg">
                    <h3 className="font-bold text-xl text-brand-text-title mb-4">Scaled Recipe Results</h3>
                    <ul className="space-y-3">
                        {ingredients.map(ing => {
                            const originalQty = parseFloat(ing.quantity) || 0;
                            const originalWeight = parseFloat(ing.weight) || 0;
                            const factor = parseFloat(scaleFactor) || 1;
                            
                            const newQty = (originalQty * factor).toLocaleString(undefined, { maximumFractionDigits: 2 });
                            const newWeight = (originalWeight * factor).toLocaleString(undefined, { maximumFractionDigits: 2 });
                            
                            let resultText = '';
                            if (originalQty > 0 && ing.unit) {
                                resultText += `${newQty} ${ing.unit}`;
                            }
                            if (originalWeight > 0) {
                                if (resultText) resultText += ' / ';
                                resultText += `${newWeight} g`;
                            }

                            return (
                                <li key={ing.id} className="flex justify-between p-3 bg-white rounded-md">
                                    <span className="font-medium text-brand-text-body">{ing.name}</span>
                                    <span className="font-bold text-brand-text-title">{resultText || '0'}</span>
                                </li>
                            );
                        })}
                    </ul>
                     <div className="mt-4 text-center">
                        <button onClick={handleCopy} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                            {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                            {copied ? 'Recipe Copied!' : 'Copy Scaled Recipe'}
                        </button>
                    </div>
                </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">How to Scale a Recipe Up or Down</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-2">
                    <p><strong>How to find your scaling factor:</strong> To properly adjust a recipe's servings, divide your desired yield by the original recipe's yield. For example, if your recipe makes 12 cupcakes and you need 30, your scaling factor (or recipe multiplier) is 2.5 (30 ÷ 12).</p>
                    <p><strong>For Best Accuracy, Use Weight:</strong> Whenever possible, enter the ingredient weight in grams. Scaling by weight is far more accurate than scaling by volume (cups, tbsp) and is the professional standard for consistent results. Our <a href={toolIdToSlug('measurement-converter')} onClick={(e) => { e.preventDefault(); setActiveTool('measurement-converter'); }} className="font-bold text-brand-pink hover:underline">Measurement Converter</a> can help you find the weight of common ingredients.</p>
                    <p><strong>Scaling Down:</strong> To make half a batch of a recipe that yields 24 cookies, use a scaling factor of 0.5. The recipe converter will then give you ingredient amounts for 12 cookies.</p>
                    <p><strong>Adjust Baking Time:</strong> When you scale a recipe, the baking time will likely change. Smaller batches bake faster, while larger batches (or batters in a deeper pan) take longer. Always keep a close eye on your oven!</p>
                </div>
            </div>
        </ToolContainer>
    );
};
