
import React, { useState, useCallback } from 'react';
import { ToolContainer } from './ToolContainer';
// Fix: Corrected import path for the ToolId type.
import type { ToolId } from '../utils/slugs';
import { ClipboardIcon, CheckCircleIcon } from '../Icons';

interface OvenTemperatureConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const GAS_MARKS: { [key: number]: { minC: number, maxC: number } } = {
    1: { minC: 120, maxC: 139 }, 2: { minC: 140, maxC: 149 }, 3: { minC: 150, maxC: 169 },
    4: { minC: 170, maxC: 179 }, 5: { minC: 180, maxC: 199 }, 6: { minC: 200, maxC: 209 },
    7: { minC: 210, maxC: 229 }, 8: { minC: 230, maxC: 239 }, 9: { minC: 240, maxC: 250 },
};

const getGasMark = (celsius: number): string => {
    if (celsius < 120) return '< 1';
    for (const mark in GAS_MARKS) {
        if (celsius >= GAS_MARKS[mark].minC && celsius <= GAS_MARKS[mark].maxC) {
            return mark;
        }
    }
    return '> 9';
};

const InputField: React.FC<{label: string, value: string, onChange: (val: string) => void}> = ({ label, value, onChange }) => (
    <div>
        <label className="block text-lg font-semibold mb-2 text-brand-text-title">{label}</label>
        <input 
            type="number" 
            value={value}
            onChange={e => onChange(e.target.value)}
            className="w-full p-4 text-3xl font-bold bg-white border-2 border-brand-border rounded-lg focus:ring-2 focus:ring-brand-pink/50 focus:border-brand-pink transition-shadow duration-200 text-brand-text-body text-center"
        />
    </div>
);

export const OvenTemperatureConverter: React.FC<OvenTemperatureConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [celsius, setCelsius] = useState('180');
    const [fahrenheit, setFahrenheit] = useState('350');
    const [gasMark, setGasMark] = useState('4');
    const [copied, setCopied] = useState(false);

    const handleCelsiusChange = useCallback((value: string) => {
        const C = parseFloat(value);
        if (isNaN(C)) {
            setCelsius(''); setFahrenheit(''); setGasMark('');
            return;
        }
        setCelsius(value);
        const F = (C * 9/5) + 32;
        setFahrenheit(Math.round(F).toString());
        setGasMark(getGasMark(C));
    }, []);

    const handleFahrenheitChange = useCallback((value: string) => {
        const F = parseFloat(value);
        if (isNaN(F)) {
            setCelsius(''); setFahrenheit(''); setGasMark('');
            return;
        }
        setFahrenheit(value);
        const C = (F - 32) * 5/9;
        setCelsius(Math.round(C).toString());
        setGasMark(getGasMark(C));
    }, []);

    const handleCopy = async () => {
        const text = `${celsius}°C = ${fahrenheit}°F = Gas Mark ${gasMark}`;
        try {
            await navigator.clipboard.writeText(text);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy results.');
        }
    };


    return (
        <ToolContainer 
            title="Free Oven Temperature Converter (°F, °C, Gas Mark)" 
            description="Quickly convert oven temperatures between Fahrenheit (°F), Celsius (°C), and Gas Mark with this free tool. The essential baking temperature conversion chart for any recipe." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="max-w-4xl mx-auto">
                <div className="grid md:grid-cols-2 gap-8">
                    <div className="bg-white p-6 rounded-xl border border-brand-border">
                        <h3 className="font-serif text-xl font-bold text-brand-text-title mb-4">Convert Celsius (°C) to Fahrenheit (°F)</h3>
                        <InputField label="Celsius (°C)" value={celsius} onChange={handleCelsiusChange} />
                        <div className="mt-4 text-center">
                            <p className="text-sm text-brand-text-body">Result in Fahrenheit:</p>
                            <p className="text-3xl font-bold text-brand-pink">{fahrenheit}°F</p>
                        </div>
                    </div>
                    <div className="bg-white p-6 rounded-xl border border-brand-border">
                        <h3 className="font-serif text-xl font-bold text-brand-text-title mb-4">Convert Fahrenheit (°F) to Celsius (°C)</h3>
                        <InputField label="Fahrenheit (°F)" value={fahrenheit} onChange={handleFahrenheitChange} />
                        <div className="mt-4 text-center">
                            <p className="text-sm text-brand-text-body">Result in Celsius:</p>
                            <p className="text-3xl font-bold text-brand-pink">{celsius}°C</p>
                        </div>
                    </div>
                </div>
                <div className="mt-8 text-center bg-brand-pink-light p-6 rounded-xl border border-brand-border">
                    <h3 className="font-serif text-xl font-bold text-brand-text-title mb-2">Equivalent Gas Mark</h3>
                    <p className="text-5xl font-bold text-brand-pink">{gasMark}</p>
                </div>
                 <div className="mt-6 text-center">
                    <button onClick={handleCopy} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                        {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                        {copied ? 'Results Copied!' : 'Copy Results'}
                    </button>
                </div>
                <div className="mt-8 pt-8 border-t border-brand-border">
                    <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title text-center">Baking Temperature Conversion Chart</h3>
                    <div className="grid sm:grid-cols-3 gap-4 text-center">
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                            <h4 className="font-bold text-brand-text-title">160°C / 325°F / Gas Mark 3</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">Good for "low and slow" bakes like cheesecakes and some rich fruit cakes to prevent cracking or burning.</p>
                        </div>
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                            <h4 className="font-bold text-brand-text-title">180°C / 350°F / Gas Mark 4</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">The most common temperature for baking. Perfect for most cakes, cookies, and brownies, ensuring an even bake.</p>
                        </div>
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                             <h4 className="font-bold text-brand-text-title">200°C / 400°F / Gas Mark 6</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">Used for items that need a quick, hot bake like puff pastry, some breads, and roasting vegetables to get a good color.</p>
                        </div>
                    </div>
                </div>
                <div className="mt-8 pt-8 border-t border-brand-border">
                    <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title text-center">Pro Tip: Use an Oven Thermometer</h3>
                    <div className="prose max-w-none text-brand-text-body/90 leading-relaxed">
                        <p>
                            Most home ovens are not perfectly calibrated. The temperature you set might be 25-50°F (15-30°C) different from the actual temperature inside. An inexpensive oven thermometer that hangs from a rack is the single best tool to ensure your bakes are not under or over-cooked. Always preheat your oven for at least 20 minutes and trust the thermometer, not the dial!
                        </p>
                    </div>
                </div>
                <div className="mt-8 text-center text-sm text-brand-text-body/80">
                    <p>Note: Gas Mark is an approximation and temperatures can vary between ovens. An oven thermometer is always recommended for accuracy when converting Celsius to Fahrenheit for your oven.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
