
import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

interface AmortizationCalculatorProps {
  setActiveTool: (toolId: ToolId) => void;
  breadcrumbs: React.ReactNode;
}

interface ScheduleEntry {
  month: number;
  principal: string;
  interest: string;
  totalPayment: string;
  remainingBalance: string;
}

export const AmortizationCalculator: React.FC<AmortizationCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
  const [loanAmount, setLoanAmount] = useState('250000');
  const [interestRate, setInterestRate] = useState('5');
  const [loanTerm, setLoanTerm] = useState('30');
  const [schedule, setSchedule] = useState<ScheduleEntry[]>([]);
  const [monthlyPayment, setMonthlyPayment] = useState('');
  const [totalInterest, setTotalInterest] = useState('');
  const [totalPayment, setTotalPayment] = useState('');
  const [error, setError] = useState('');

  const calculateAmortization = () => {
    const P = parseFloat(loanAmount);
    const i = parseFloat(interestRate) / 100 / 12;
    const n = parseFloat(loanTerm) * 12;

    if (isNaN(P) || isNaN(i) || isNaN(n) || P <= 0 || i <= 0 || n <= 0) {
      setError('Please enter valid numbers for all fields.');
      setSchedule([]);
      setMonthlyPayment('');
      setTotalInterest('');
      setTotalPayment('');
      return;
    }
    setError('');

    const M = P * (i * Math.pow(1 + i, n)) / (Math.pow(1 + i, n) - 1);
    setMonthlyPayment(M.toFixed(2));

    let remainingBalance = P;
    const newSchedule: ScheduleEntry[] = [];
    let cumulativeInterest = 0;

    for (let month = 1; month <= n; month++) {
      const interestPayment = remainingBalance * i;
      const principalPayment = M - interestPayment;
      remainingBalance -= principalPayment;
      cumulativeInterest += interestPayment;

      newSchedule.push({
        month,
        principal: principalPayment.toFixed(2),
        interest: interestPayment.toFixed(2),
        totalPayment: M.toFixed(2),
        remainingBalance: remainingBalance.toFixed(2),
      });
    }

    setSchedule(newSchedule);
    setTotalInterest(cumulativeInterest.toFixed(2));
    setTotalPayment((P + cumulativeInterest).toFixed(2));
  };

  const chartData = {
    labels: schedule.map(s => `Month ${s.month}`),
    datasets: [
      {
        label: 'Principal',
        data: schedule.map(s => parseFloat(s.principal)),
        backgroundColor: 'rgba(230, 90, 136, 0.6)',
      },
      {
        label: 'Interest',
        data: schedule.map(s => parseFloat(s.interest)),
        backgroundColor: 'rgba(74, 63, 63, 0.6)',
      },
    ],
  };

  return (
    <ToolContainer
      title="Amortization Calculator"
      description="Calculate your loan payments and see how the principal and interest change over time."
      setActiveTool={setActiveTool}
      breadcrumbs={breadcrumbs}
    >
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div>
          <label htmlFor="loanAmount" className="block text-sm font-medium text-brand-text-body">Loan Amount ($)</label>
          <input
            type="number"
            id="loanAmount"
            value={loanAmount}
            onChange={(e) => setLoanAmount(e.target.value)}
            className="mt-1 block w-full px-3 py-2 bg-white border border-brand-border rounded-md shadow-sm focus:outline-none focus:ring-brand-pink focus:border-brand-pink sm:text-sm"
          />
        </div>
        <div>
          <label htmlFor="interestRate" className="block text-sm font-medium text-brand-text-body">Annual Interest Rate (%)</label>
          <input
            type="number"
            id="interestRate"
            value={interestRate}
            onChange={(e) => setInterestRate(e.target.value)}
            className="mt-1 block w-full px-3 py-2 bg-white border border-brand-border rounded-md shadow-sm focus:outline-none focus:ring-brand-pink focus:border-brand-pink sm:text-sm"
          />
        </div>
        <div>
          <label htmlFor="loanTerm" className="block text-sm font-medium text-brand-text-body">Loan Term (Years)</label>
          <input
            type="number"
            id="loanTerm"
            value={loanTerm}
            onChange={(e) => setLoanTerm(e.target.value)}
            className="mt-1 block w-full px-3 py-2 bg-white border border-brand-border rounded-md shadow-sm focus:outline-none focus:ring-brand-pink focus:border-brand-pink sm:text-sm"
          />
        </div>
      </div>
      <button
        onClick={calculateAmortization}
        className="w-full bg-brand-pink text-white font-bold py-2 px-4 rounded-lg hover:opacity-90 transition-opacity"
      >
        Calculate
      </button>

      {error && <p className="text-red-500 mt-4">{error}</p>}

      {monthlyPayment && (
        <div className="mt-6 bg-white p-6 rounded-xl border border-brand-border">
          <h2 className="text-2xl font-bold text-brand-text-title mb-4">Results</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
            <div>
              <p className="text-sm font-semibold text-brand-text-body">Monthly Payment</p>
              <p className="text-2xl font-bold text-brand-pink">${monthlyPayment}</p>
            </div>
            <div>
              <p className="text-sm font-semibold text-brand-text-body">Total Interest Paid</p>
              <p className="text-2xl font-bold text-brand-pink">${totalInterest}</p>
            </div>
            <div>
              <p className="text-sm font-semibold text-brand-text-body">Total of Payments</p>
              <p className="text-2xl font-bold text-brand-pink">${totalPayment}</p>
            </div>
          </div>
        </div>
      )}

      {schedule.length > 0 && (
        <div className="mt-6">
            <div className="bg-white p-4 rounded-xl border border-brand-border mb-6">
                <Bar options={{
                    responsive: true,
                    plugins: {
                        legend: { position: 'top' as const },
                        title: { display: true, text: 'Principal vs. Interest by Year' },
                    },
                    scales: {
                        x: { stacked: true },
                        y: { stacked: true },
                    },
                }} data={chartData} />
            </div>

          <div className="overflow-x-auto relative shadow-md sm:rounded-lg" style={{ maxHeight: '500px' }}>
            <table className="w-full text-sm text-left text-brand-text-body">
              <thead className="text-xs text-brand-text-title uppercase bg-brand-pink-light sticky top-0">
                <tr>
                  <th scope="col" className="py-3 px-6">Month</th>
                  <th scope="col" className="py-3 px-6">Principal</th>
                  <th scope="col" className="py-3 px-6">Interest</th>
                  <th scope="col" className="py-3 px-6">Total Payment</th>
                  <th scope="col" className="py-3 px-6">Remaining Balance</th>
                </tr>
              </thead>
              <tbody>
                {schedule.map((entry) => (
                  <tr key={entry.month} className="bg-white border-b border-brand-border">
                    <td className="py-4 px-6">{entry.month}</td>
                    <td className="py-4 px-6">${entry.principal}</td>
                    <td className="py-4 px-6">${entry.interest}</td>
                    <td className="py-4 px-6">${entry.totalPayment}</td>
                    <td className="py-4 px-6">${entry.remainingBalance}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </ToolContainer>
  );
};

