import React from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { ShoppingCartIcon } from '../Icons';

interface ShoppingListProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const ShoppingList: React.FC<ShoppingListProps> = ({ setActiveTool, breadcrumbs }) => {
    return (
        <ToolContainer 
            title="Free Baking Shopping List Generator" 
            description="Create, manage, and organize your shopping lists for all your baking ingredients for free. Never forget an item at the store again." 
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="text-center py-16 bg-brand-pink-light/60 rounded-lg">
                <ShoppingCartIcon className="w-12 h-12 mx-auto text-brand-text-body/50 mb-4"/>
                <h3 className="font-bold text-brand-text-title">Shopping List Feature Coming Soon!</h3>
                <p className="text-sm text-brand-text-body/80">This tool will help you organize your ingredient needs.</p>
            </div>
        </ToolContainer>
    );
};
