import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';

interface FlavorPairingGuideProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const flavorData: Record<string, Record<string, string[]>> = {
    'Chocolate': {
        'Classic Pairings': ['Vanilla', 'Caramel', 'Raspberry', 'Peanut Butter', 'Coffee'],
        'Fruits': ['Orange', 'Strawberry', 'Cherry', 'Banana', 'Pear'],
        'Spices & Herbs': ['Cinnamon', 'Chili', 'Cardamom', 'Mint', 'Lavender'],
        'Nuts & Other': ['Hazelnut (Nutella)', 'Almond', 'Coconut', 'Sea Salt', 'Bacon'],
    },
    'Vanilla': {
        'Classic Pairings': ['Strawberry', 'Chocolate', 'Caramel', 'Lemon', 'Blueberry'],
        'Fruits': ['Peach', 'Cherry', 'Apple', 'Orange', 'Fig'],
        'Spices & Herbs': ['Cardamom', 'Cinnamon', 'Lavender', 'Rosemary', 'Nutmeg'],
        'Nuts & Other': ['Almond', 'Pecan', 'Pistachio', 'Coffee', 'Honey'],
    },
    'Lemon': {
        'Classic Pairings': ['Blueberry', 'Raspberry', 'Poppy Seed', 'Vanilla', 'Meringue'],
        'Fruits': ['Strawberry', 'Blackberry', 'Coconut', 'Lime', 'Peach'],
        'Spices & Herbs': ['Thyme', 'Rosemary', 'Lavender', 'Ginger', 'Mint'],
        'Nuts & Other': ['Almond', 'Pistachio', 'White Chocolate', 'Honey', 'Gin'],
    },
    'Strawberry': {
        'Classic Pairings': ['Vanilla (Cream)', 'Chocolate', 'Lemon', 'Rhubarb', 'Banana'],
        'Fruits': ['Lime', 'Coconut', 'Peach', 'Watermelon', 'Kiwi'],
        'Spices & Herbs': ['Basil', 'Mint', 'Black Pepper', 'Balsamic Vinegar', 'Rose'],
        'Nuts & Other': ['Almond', 'Pistachio', 'White Chocolate', 'Cream Cheese', 'Champagne'],
    },
    'Apple': {
        'Classic Pairings': ['Cinnamon', 'Caramel', 'Cheddar Cheese', 'Walnut', 'Oats'],
        'Fruits': ['Cranberry', 'Pear', 'Blackberry', 'Lemon', 'Fig'],
        'Spices & Herbs': ['Nutmeg', 'Ginger', 'Cardamom', 'Thyme', 'Rosemary'],
        'Nuts & Other': ['Pecan', 'Almond', 'Maple Syrup', 'Bourbon', 'Pork'],
    },
    'Coffee': {
        'Classic Pairings': ['Chocolate', 'Caramel', 'Vanilla', 'Hazelnut', 'Cinnamon'],
        'Fruits': ['Orange', 'Cherry', 'Date', 'Banana', 'Fig'],
        'Spices & Herbs': ['Cardamom', 'Nutmeg', 'Anise', 'Chicory'],
        'Nuts & Other': ['Almond', 'Walnut', 'Whiskey', 'Dark Chocolate', 'Cream'],
    },
    'Caramel': {
        'Classic Pairings': ['Sea Salt', 'Apple', 'Chocolate', 'Banana', 'Coffee'],
        'Fruits': ['Pear', 'Fig', 'Peach', 'Apricot'],
        'Spices & Herbs': ['Cinnamon', 'Ginger', 'Vanilla Bean'],
        'Nuts & Other': ['Pecan', 'Walnut', 'Bourbon', 'Pretzels', 'Popcorn'],
    },
    'Lavender': {
        'Classic Pairings': ['Honey', 'Lemon', 'Blueberry', 'Vanilla'],
        'Fruits': ['Blackberry', 'Fig', 'Pear', 'Apricot'],
        'Spices & Herbs': ['Rosemary', 'Thyme', 'Mint'],
        'Nuts & Other': ['White Chocolate', 'Gin', 'Almond', 'Earl Grey Tea'],
    },
};

const primaryFlavors = Object.keys(flavorData);

export const FlavorPairingGuide: React.FC<FlavorPairingGuideProps> = ({ setActiveTool, breadcrumbs }) => {
    const [selectedFlavor, setSelectedFlavor] = useState('Chocolate');
    const pairings = flavorData[selectedFlavor];

    return (
        <ToolContainer
            title="Free Flavor Pairing Guide for Baking"
            description="Discover the perfect flavor combinations for your cakes, cookies, and pastries. Our free, interactive guide helps you pair ingredients like a pro."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-3 gap-8">
                {/* Flavor Selection */}
                <div className="md:col-span-1">
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-4">Choose a Flavor</h3>
                    <div className="space-y-2">
                        {primaryFlavors.map(flavor => (
                            <button
                                key={flavor}
                                onClick={() => setSelectedFlavor(flavor)}
                                className={`w-full text-left p-3 rounded-lg font-semibold transition-all duration-200 ${selectedFlavor === flavor ? 'bg-brand-pink text-white shadow-md' : 'bg-brand-pink-light/60 hover:bg-white'}`}
                            >
                                {flavor}
                            </button>
                        ))}
                    </div>
                </div>

                {/* Pairings Display */}
                <div className="md:col-span-2">
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-4">
                        Pairs well with <span className="text-brand-pink">{selectedFlavor}</span>
                    </h3>
                    <div className="space-y-4">
                        {Object.entries(pairings).map(([category, flavors]) => (
                            <div key={category} className="p-4 bg-white rounded-lg border border-brand-border">
                                <h4 className="font-bold text-brand-text-title mb-2">{category}</h4>
                                <div className="flex flex-wrap gap-2">
                                    {flavors.map(f => (
                                        <span key={f} className="bg-gray-200 text-gray-800 text-sm font-medium px-3 py-1 rounded-full">
                                            {f}
                                        </span>
                                    ))}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Pro Tips for Flavor Pairing</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <ul>
                        <li><strong>Balance is Key:</strong> Great pairings often balance contrasting profiles. Pair a sweet flavor like caramel with something salty (sea salt) or tart (apple). Pair a rich flavor like chocolate with something bright and acidic (raspberry).</li>
                        <li><strong>Consider Intensity:</strong> Match the intensity of your flavors. A delicate flavor like lavender can be easily overpowered, so pair it with other subtle notes like honey or white chocolate. A bold flavor like coffee can stand up to strong partners like dark chocolate or whiskey.</li>
                        <li><strong>Think About Texture:</strong> Flavor isn't just about taste. Contrast creamy textures with crunchy ones. For example, a creamy cheesecake is enhanced by a crunchy graham cracker crust and crunchy pecans.</li>
                        <li><strong>Start Simple:</strong> When experimenting, start by adding just one or two new flavors to a familiar base. This helps you understand how each new ingredient impacts the final taste.</li>
                    </ul>
                </div>
            </div>
        </ToolContainer>
    );
};
