import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { MortgageIcon, InfoIcon } from '../Icons';

interface MortgageAffordabilityCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const InputField: React.FC<{ label: string, id: string, value: string, onChange: (val: string) => void, prefix?: string, suffix?: string }> = ({ label, id, value, onChange, prefix, suffix }) => (
    <div>
        <label htmlFor={id} className="block text-sm font-bold text-brand-text-title mb-1">{label}</label>
        <div className="relative">
            {prefix && <span className="absolute left-3 top-1/2 -translate-y-1/2 text-brand-text-body/60">{prefix}</span>}
            <input
                id={id}
                type="number"
                value={value}
                onChange={e => onChange(e.target.value)}
                className={`w-full px-4 py-2.5 bg-white border border-brand-border rounded-lg ${prefix ? 'pl-7' : ''} ${suffix ? 'pr-10' : ''}`}
            />
            {suffix && <span className="absolute right-3 top-1/2 -translate-y-1/2 text-brand-text-body/60">{suffix}</span>}
        </div>
    </div>
);

export const MortgageAffordabilityCalculator: React.FC<MortgageAffordabilityCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
    const [annualIncome, setAnnualIncome] = useState('75000');
    const [monthlyDebts, setMonthlyDebts] = useState('500');
    const [downPayment, setDownPayment] = useState('20000');
    const [interestRate, setInterestRate] = useState('6.5');
    const [loanTerm, setLoanTerm] = useState('30');
    const [propertyTax, setPropertyTax] = useState('1.2');
    const [homeInsurance, setHomeInsurance] = useState('0.5');

    const affordableHomePrice = useMemo(() => {
        const income = parseFloat(annualIncome) || 0;
        const debts = parseFloat(monthlyDebts) || 0;
        const down = parseFloat(downPayment) || 0;
        const rate = parseFloat(interestRate) / 100 || 0;
        const term = parseInt(loanTerm) || 0;
        const taxRate = parseFloat(propertyTax) / 100 || 0;
        const insuranceRate = parseFloat(homeInsurance) / 100 || 0;

        if (income <= 0 || rate <= 0 || term <= 0) return 0;

        const monthlyIncome = income / 12;
        const monthlyRate = rate / 12;
        const numberOfPayments = term * 12;

        // Based on 28/36 rule
        const maxHousingPayment1 = monthlyIncome * 0.28;
        const maxHousingPayment2 = monthlyIncome * 0.36 - debts;
        const maxMonthlyPITI = Math.max(0, Math.min(maxHousingPayment1, maxHousingPayment2));

        // Estimate PITI as a percentage of the loan amount to solve for the loan amount
        // This is an iterative approximation, but a direct formula can estimate it
        const monthlyTaxesAndInsuranceFactor = (taxRate + insuranceRate) / 12;
        const monthlyPaymentFactor = (monthlyRate * Math.pow(1 + monthlyRate, numberOfPayments)) / (Math.pow(1 + monthlyRate, numberOfPayments) - 1);

        const maxLoanAmount = maxMonthlyPITI / (monthlyPaymentFactor + monthlyTaxesAndInsuranceFactor);
        
        return Math.round(maxLoanAmount + down);

    }, [annualIncome, monthlyDebts, downPayment, interestRate, loanTerm, propertyTax, homeInsurance]);

    return (
        <ToolContainer
            title="Mortgage Affordability Calculator"
            description="Estimate the home price you can afford based on your income, debts, and down payment. This tool uses the 28/36 rule to provide a realistic home affordability estimate."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="grid md:grid-cols-2 gap-8 items-start">
                <div className="space-y-4">
                    <InputField label="Annual Gross Income" id="income" value={annualIncome} onChange={setAnnualIncome} prefix="$" />
                    <InputField label="Total Monthly Debts" id="debts" value={monthlyDebts} onChange={setMonthlyDebts} prefix="$" />
                    <InputField label="Down Payment" id="downPayment" value={downPayment} onChange={setDownPayment} prefix="$" />
                    <div className="grid grid-cols-2 gap-4">
                        <InputField label="Interest Rate" id="interestRate" value={interestRate} onChange={setInterestRate} suffix="%" />
                        <InputField label="Loan Term" id="loanTerm" value={loanTerm} onChange={setLoanTerm} suffix="yrs" />
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                        <InputField label="Property Tax Rate" id="propertyTax" value={propertyTax} onChange={setPropertyTax} suffix="%" />
                        <InputField label="Home Insurance Rate" id="homeInsurance" value={homeInsurance} onChange={setHomeInsurance} suffix="%" />
                    </div>
                </div>

                <div className="bg-brand-pink-light p-6 rounded-2xl text-center sticky top-6">
                    <MortgageIcon className="w-12 h-12 mx-auto text-brand-pink mb-2"/>
                    <h3 className="font-serif font-bold text-2xl text-brand-text-title mb-2">Affordable Home Price</h3>
                    <div className="bg-white p-4 rounded-lg border border-brand-pink/20">
                        <p className="font-serif font-bold text-6xl text-brand-pink">${affordableHomePrice.toLocaleString()}</p>
                    </div>
                    <p className="text-xs text-brand-text-body/70 mt-4">
                        This is an estimate. Your final approved loan amount may vary.
                    </p>
                </div>
            </div>

            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">How Affordability is Calculated</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>This calculator uses the <strong>28/36 rule</strong>, a common guideline used by lenders to assess affordability.</p>
                    <ul className="list-disc pl-5 space-y-2">
                        <li>
                            <strong>Front-End Ratio (28%):</strong> Your total monthly housing payment (Principal, Interest, Taxes, and Insurance - PITI) should not exceed 28% of your gross monthly income.
                        </li>
                        <li>
                            <strong>Back-End Ratio (36%):</strong> Your total monthly debt payments (PITI plus other debts like car loans, student loans, and credit card payments) should not exceed 36% of your gross monthly income.
                        </li>
                    </ul>
                    <p>The calculator determines the maximum affordable home price based on the more conservative of these two ratios to give you a realistic estimate.</p>
                    <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
                        <p className="flex items-start gap-2"><InfoIcon className="w-5 h-5 text-blue-500 flex-shrink-0 mt-0.5"/><span><strong>Disclaimer:</strong> This is an estimation tool for informational purposes only. Consult with a financial advisor or mortgage lender for personalized advice.</span></p>
                    </div>
                </div>
            </div>
        </ToolContainer>
    );
};
