import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
// Fix: Corrected import path for ToolId.
import type { ToolId } from '../utils/slugs';
import { DollarSignIcon, ClipboardIcon, CheckCircleIcon, UsersIcon } from '../Icons';

interface TipCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const TipCalculator: React.FC<TipCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
    const [bill, setBill] = useState('50.00');
    const [tipPercent, setTipPercent] = useState('20');
    const [customTipPercent, setCustomTipPercent] = useState('');
    const [numPeople, setNumPeople] = useState('1');
    const [copied, setCopied] = useState<string | null>(null);

    const handleCopy = (value: string, field: string) => {
        navigator.clipboard.writeText(value).then(() => {
            setCopied(field);
            setTimeout(() => setCopied(null), 2000);
        });
    };

    const { tipAmount, totalAmount, tipPerPerson, totalPerPerson } = useMemo(() => {
        const billAmount = parseFloat(bill) || 0;
        const peopleCount = parseInt(numPeople) || 1;
        const percent = parseFloat(customTipPercent || tipPercent) / 100;

        const tip = billAmount * percent;
        const total = billAmount + tip;
        const tipPer = tip / peopleCount;
        const totalPer = total / peopleCount;

        return {
            tipAmount: tip.toFixed(2),
            totalAmount: total.toFixed(2),
            tipPerPerson: tipPer.toFixed(2),
            totalPerPerson: total.toFixed(2),
        };
    }, [bill, tipPercent, customTipPercent, numPeople]);

    const handleTipSelect = (percent: string) => {
        setTipPercent(percent);
        setCustomTipPercent('');
    };
    
    const handleCustomTipChange = (value: string) => {
        setCustomTipPercent(value);
        if (value !== '') {
            setTipPercent('');
        } else if (tipPercent === '') {
            handleTipSelect('20'); // Default back if custom is cleared
        }
    }

    return (
        <ToolContainer title="Free Tip Calculator" description="Quickly calculate the tip amount and split the bill between any number of people with our free tool." setActiveTool={setActiveTool} breadcrumbs={breadcrumbs}>
            <div className="grid lg:grid-cols-2 gap-8">
                <div className="space-y-6">
                    <div>
                        <label htmlFor="billAmount" className="text-lg font-bold text-brand-text-title mb-2 block">Bill Amount</label>
                        <div className="relative">
                            <DollarSignIcon className="absolute left-3 top-1/2 -translate-y-1/2 w-6 h-6 text-brand-text-body/40" />
                            <input id="billAmount" type="number" value={bill} onChange={e => setBill(e.target.value)} className="w-full pl-12 pr-4 py-3 bg-white border-2 border-brand-border rounded-lg text-3xl font-bold" />
                        </div>
                    </div>
                    <div>
                        <label className="text-lg font-bold text-brand-text-title mb-2 block">Select Tip %</label>
                        <div className="grid grid-cols-3 gap-2">
                            {['15', '18', '20'].map(p => (
                                <button key={p} onClick={() => handleTipSelect(p)} className={`py-3 font-bold rounded-lg transition-colors ${tipPercent === p ? 'bg-brand-pink text-white' : 'bg-brand-pink-light text-brand-pink hover:bg-white/50'}`}>
                                    {p}%
                                </button>
                            ))}
                             <input type="number" value={customTipPercent} onChange={e => handleCustomTipChange(e.target.value)} placeholder="Custom %" className={`py-3 font-bold rounded-lg text-center col-span-3 transition-colors text-brand-pink placeholder-brand-pink/70 ${customTipPercent ? 'bg-brand-pink text-white placeholder-white/70' : 'bg-brand-pink-light'}`} />
                        </div>
                    </div>
                     <div>
                        <label htmlFor="numPeople" className="text-lg font-bold text-brand-text-title mb-2 block">Split Between</label>
                        <div className="relative">
                            <UsersIcon className="absolute left-3 top-1/2 -translate-y-1/2 w-6 h-6 text-brand-text-body/40" />
                            <input id="numPeople" type="number" min="1" step="1" value={numPeople} onChange={e => setNumPeople(e.target.value)} className="w-full pl-12 pr-4 py-3 bg-white border-2 border-brand-border rounded-lg text-3xl font-bold" />
                        </div>
                    </div>
                </div>
                <div className="bg-brand-pink-light p-6 rounded-2xl flex flex-col justify-center">
                    <div className="space-y-4">
                        <div className="flex justify-between items-center">
                            <span className="font-semibold text-brand-text-body">Tip Amount</span>
                            <span className="font-bold text-2xl text-brand-text-title">${tipAmount}</span>
                        </div>
                         <div className="flex justify-between items-center">
                            <span className="font-semibold text-brand-text-body">Total Bill</span>
                            <span className="font-bold text-2xl text-brand-text-title">${totalAmount}</span>
                        </div>
                    </div>
                    {parseInt(numPeople) > 1 && (
                         <div className="mt-6 pt-6 border-t-2 border-brand-pink/20 space-y-4">
                            <div className="flex justify-between items-center">
                                <span className="font-semibold text-brand-text-body">Tip per Person</span>
                                <span className="font-bold text-2xl text-brand-text-title">${tipPerPerson}</span>
                            </div>
                            <div className="flex justify-between items-center">
                                <span className="font-semibold text-brand-text-body">Total per Person</span>
                                <span className="font-bold text-2xl text-brand-text-title">${totalPerPerson}</span>
                            </div>
                        </div>
                    )}
                    <div className="mt-6 pt-6 border-t-2 border-brand-pink/20 text-center">
                         <button onClick={() => handleCopy(totalPerPerson, 'total')} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                            {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                            {copied ? 'Total Per Person Copied!' : 'Copy Total Per Person'}
                        </button>
                    </div>
                </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">A Quick Guide to Tipping</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed">
                    <p>Tipping customs can vary, but here are some general guidelines in the U.S.:</p>
                    <ul>
                        <li><strong>Sit-Down Restaurants:</strong> 15-20% of the pre-tax bill is standard for good service.</li>
                        <li><strong>Food Delivery:</strong> 10-15% is customary. Consider tipping more for large orders or bad weather.</li>
                        <li><strong>Coffee Shops/Counter Service:</strong> While not always expected, tipping $1-$2 or 10-20% for larger orders is a nice gesture for good service.</li>
                    </ul>
                </div>
            </div>
        </ToolContainer>
    );
};
