import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';

interface MortgageCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const MortgageCalculator: React.FC<MortgageCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
  const [loanAmount, setLoanAmount] = useState<number | ''>('');
  const [interestRate, setInterestRate] = useState<number | ''>('');
  const [loanTerm, setLoanTerm] = useState<number | ''>('');
  const [monthlyPayment, setMonthlyPayment] = useState<number | null>(null);

  const calculateMonthlyPayment = () => {
    if (loanAmount && interestRate && loanTerm) {
      const principal = loanAmount;
      const monthlyInterestRate = interestRate / 100 / 12;
      const numberOfPayments = loanTerm * 12;

      if (monthlyInterestRate === 0) {
        setMonthlyPayment(principal / numberOfPayments);
        return;
      }

      const numerator = monthlyInterestRate * Math.pow(1 + monthlyInterestRate, numberOfPayments);
      const denominator = Math.pow(1 + monthlyInterestRate, numberOfPayments) - 1;
      const monthlyPayment = principal * (numerator / denominator);

      setMonthlyPayment(monthlyPayment);
    }
  };

  return (
    <ToolContainer
        title="Mortgage Calculator"
        description="Estimate your monthly mortgage payments for free. See how much you'll pay in interest over the life of the loan."
        setActiveTool={setActiveTool}
        breadcrumbs={breadcrumbs}
    >
        <div className="max-w-md mx-auto p-4 sm:p-6 bg-white rounded-2xl border border-brand-border shadow-sm">
          <div className="mb-6">
            <label className="block text-sm font-bold text-brand-text-title mb-2" htmlFor="loanAmount">Loan Amount ($)</label>
            <input
              id="loanAmount"
              type="number"
              className="w-full p-3 bg-gray-50 border border-gray-300 rounded-lg focus:ring-2 focus:ring-brand-pink focus:border-transparent transition-shadow"
              value={loanAmount}
              onChange={(e) => setLoanAmount(Number(e.target.value))}
              placeholder="e.g., 300000"
            />
          </div>
          <div className="mb-6">
            <label className="block text-sm font-bold text-brand-text-title mb-2" htmlFor="interestRate">Interest Rate (%)</label>
            <input
              id="interestRate"
              type="number"
              className="w-full p-3 bg-gray-50 border border-gray-300 rounded-lg focus:ring-2 focus:ring-brand-pink focus:border-transparent transition-shadow"
              value={interestRate}
              onChange={(e) => setInterestRate(Number(e.target.value))}
              placeholder="e.g., 3.5"
            />
          </div>
          <div className="mb-6">
            <label className="block text-sm font-bold text-brand-text-title mb-2" htmlFor="loanTerm">Loan Term (Years)</label>
            <input
              id="loanTerm"
              type="number"
              className="w-full p-3 bg-gray-50 border border-gray-300 rounded-lg focus:ring-2 focus:ring-brand-pink focus:border-transparent transition-shadow"
              value={loanTerm}
              onChange={(e) => setLoanTerm(Number(e.target.value))}
              placeholder="e.g., 30"
            />
          </div>
          <button
            onClick={calculateMonthlyPayment}
            className="w-full p-4 bg-brand-pink text-white font-bold rounded-lg hover:bg-opacity-90 transition-all duration-300 transform hover:scale-105"
          >
            Calculate Monthly Payment
          </button>
          {monthlyPayment !== null && (
            <div className="mt-8 p-6 bg-brand-pink-light rounded-lg border border-brand-pink/20 text-center">
              <p className="text-lg font-semibold text-brand-text-title">Your Estimated Monthly Payment:</p>
              <p className="text-4xl font-bold text-brand-pink mt-2">${monthlyPayment.toFixed(2)}</p>
            </div>
          )}
        </div>
    </ToolContainer>
  );
};
