import React, { useState, useMemo } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { SearchIcon, ChevronRightIcon } from '../Icons';

interface IngredientGlossaryProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const glossaryData = [
    {
        term: 'All-Purpose Flour',
        definition: 'A versatile wheat flour with a moderate protein content (around 10-12%). It provides a good balance of strength and tenderness, making it suitable for a wide range of baked goods like cookies, cakes, and some breads.',
        category: 'Flours'
    },
    {
        term: 'Bread Flour',
        definition: 'A high-protein flour (around 12-14%) that develops strong gluten strands. This strength is essential for creating the chewy texture and airy structure of yeast breads like sourdough and baguettes.',
        category: 'Flours'
    },
    {
        term: 'Cake Flour',
        definition: 'A low-protein flour (around 7-9%) that is finely milled. It produces a very soft, tender crumb, making it ideal for delicate cakes like angel food and chiffon.',
        category: 'Flours'
    },
    {
        term: 'Baking Soda (Sodium Bicarbonate)',
        definition: 'A chemical leavener that requires an acid (like buttermilk, lemon juice, or brown sugar) to react and produce carbon dioxide, which makes baked goods rise. It provides a "spread" and a tender crumb.',
        category: 'Leaveners'
    },
    {
        term: 'Baking Powder',
        definition: 'A complete chemical leavener containing both baking soda (a base) and an acid. Most are "double-acting," meaning they react once when mixed with liquid and again when heated. It provides a "lift" or "puff".',
        category: 'Leaveners'
    },
    {
        term: 'Yeast',
        definition: 'A living organism that consumes sugar and produces carbon dioxide gas, which leavens bread dough. Active Dry Yeast needs to be activated in warm water, while Instant Yeast can often be mixed directly with dry ingredients.',
        category: 'Leaveners'
    },
    {
        term: 'Unsalted Butter',
        definition: 'Butter with no added salt. It is the standard for baking because it allows the baker to control the total amount of salt in the recipe. It typically contains around 80% butterfat.',
        category: 'Fats'
    },
    {
        term: 'Dutch-Processed Cocoa Powder',
        definition: 'Cocoa powder that has been treated with an alkali to neutralize its acidity. It has a darker color and a smoother, less bitter flavor than natural cocoa. It should be used with baking powder.',
        category: 'Chocolates & Sugars'
    },
    {
        term: 'Cream of Tartar',
        definition: 'An acidic powder that is a byproduct of winemaking. In baking, it is used to stabilize whipped egg whites in meringues and angel food cakes, and it acts as the acid to activate baking soda in some recipes like snickerdoodles.',
        category: 'Additives'
    },
];

const FaqItem: React.FC<{ title: string; children: React.ReactNode }> = ({ title, children }) => (
    <details className="border-b border-brand-border py-4 group">
        <summary className="p-1 cursor-pointer flex justify-between items-center font-bold text-brand-text-title group-hover:text-brand-pink transition-colors">
            {title}
            <ChevronRightIcon className="accordion-arrow w-5 h-5 text-brand-pink transition-transform duration-300"/>
        </summary>
        <div className="px-1 pt-2 text-brand-text-body/90">
            {children}
        </div>
    </details>
);


export const IngredientGlossary: React.FC<IngredientGlossaryProps> = ({ setActiveTool, breadcrumbs }) => {
    const [searchTerm, setSearchTerm] = useState('');

    const filteredData = useMemo(() => {
        if (!searchTerm) return glossaryData;
        const lowerCaseSearch = searchTerm.toLowerCase();
        return glossaryData.filter(item => 
            item.term.toLowerCase().includes(lowerCaseSearch) ||
            item.definition.toLowerCase().includes(lowerCaseSearch)
        );
    }, [searchTerm]);

    return (
        <ToolContainer
            title="Free Baking Ingredient Glossary"
            description="Your free quick reference guide to common baking ingredients. Understand the 'why' behind your recipes by learning about different flours, sugars, and leaveners."
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="max-w-3xl mx-auto">
                <div className="mb-6 relative">
                    <SearchIcon className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                    <input
                        type="text"
                        placeholder="Search ingredients (e.g., 'bread flour', 'leavener')..."
                        value={searchTerm}
                        onChange={e => setSearchTerm(e.target.value)}
                        className="w-full pl-12 pr-4 py-3 bg-brand-pink-light/60 border border-brand-border rounded-lg focus:ring-2 focus:ring-brand-pink/50 focus:border-brand-pink transition-shadow duration-200"
                        aria-label="Search ingredients"
                    />
                </div>

                <div className="space-y-2">
                    {filteredData.length > 0 ? (
                        filteredData.map(item => (
                            <FaqItem key={item.term} title={item.term}>
                                <p>{item.definition}</p>
                            </FaqItem>
                        ))
                    ) : (
                        <div className="text-center py-12 text-brand-text-body/70">
                            <p className="font-bold text-lg text-brand-text-title">No results for "{searchTerm}"</p>
                            <p>Try searching for a different ingredient.</p>
                        </div>
                    )}
                </div>
            </div>
        </ToolContainer>
    );
};
