import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
// Fix: Corrected import path for ToolId.
import type { ToolId } from '../utils/slugs';
import { CheckCircleIcon } from '../Icons';

interface SimpleCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const SimpleCalculator: React.FC<SimpleCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
    const [display, setDisplay] = useState('0');
    const [expression, setExpression] = useState('');
    const [copied, setCopied] = useState(false);

    const handleButtonClick = (value: string) => {
        if (value === 'C') {
            setDisplay('0');
            setExpression('');
        } else if (value === '=') {
            try {
                // Using Function constructor for safer evaluation than eval()
                const sanitizedExpression = expression.replace(/×/g, '*').replace(/÷/g, '/');
                // Basic validation to prevent malicious code. Allows numbers, operators, dots, and parentheses.
                if (/[^0-9.+\-*/().\s]/.test(sanitizedExpression)) {
                    throw new Error("Invalid characters in expression");
                }
                const result = new Function('return ' + sanitizedExpression)();

                if (typeof result !== 'number' || !isFinite(result)) {
                    throw new Error("Invalid calculation result");
                }
                
                const roundedResult = Math.round(result * 10000) / 10000; // Round to 4 decimal places
                setDisplay(String(roundedResult));
                setExpression(String(roundedResult));
            } catch (error) {
                setDisplay('Error');
                setExpression('');
            }
        } else {
            if (display === '0' || display === 'Error') {
                setDisplay(value);
                setExpression(value);
            } else {
                setDisplay(display + value);
                setExpression(expression + value);
            }
        }
    };

    const handleCopy = async () => {
        if (display !== 'Error' && !copied) { // Prevent re-copying while "Copied!" is shown
            try {
                await navigator.clipboard.writeText(display);
                setCopied(true);
                setTimeout(() => setCopied(false), 1500);
            } catch (err) {
                console.error("Failed to copy text:", err);
            }
        }
    };
    
    const Button: React.FC<{val: string; className?: string}> = ({val, className}) => {
        const isOperator = ['÷', '×', '-', '+', '='].includes(val);
        const isClear = val === 'C';
        
        return (
            <button 
                onClick={() => handleButtonClick(val)}
                className={`
                    text-2xl font-semibold rounded-lg transition-all duration-200 transform active:scale-95
                    h-16
                    ${isOperator ? 'bg-brand-pink text-white hover:bg-opacity-90' : ''}
                    ${isClear ? 'bg-rose-400 text-white hover:bg-rose-500' : ''}
                    ${!isOperator && !isClear ? 'bg-gray-50 text-brand-text-title hover:bg-gray-100 border border-gray-200' : ''}
                    ${className || ''}
                `}
            >
                {val}
            </button>
        );
    };

    return (
        <ToolContainer 
            title="Simple Online Calculator" 
            description="A free, basic online calculator for quick and easy math. Perfect for simple arithmetic while you're baking or working." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="max-w-xs mx-auto p-4 bg-rose-50 rounded-2xl shadow-inner border border-rose-200/50">
                <button 
                    onClick={handleCopy} 
                    className="w-full h-24 bg-white rounded-lg mb-4 border border-brand-border text-brand-text-title text-right transition-colors duration-200 hover:bg-gray-50 flex items-center justify-end p-4 relative"
                    title="Click to copy result"
                    aria-label={`Calculator display shows ${display}. Click to copy the result.`}
                    aria-live="polite"
                >
                    {copied ? (
                        <div className="absolute inset-0 flex items-center justify-center gap-2 text-green-500 fade-in" aria-label="Copied to clipboard">
                            <CheckCircleIcon className="w-8 h-8"/>
                            <span className="text-3xl font-bold">Copied!</span>
                        </div>
                    ) : (
                        <div className="text-5xl font-sans font-light overflow-x-auto break-all w-full">
                            {display}
                        </div>
                    )}
                </button>
                <div className="grid grid-cols-4 gap-2">
                    <Button val="C" className="col-span-2" />
                    <Button val="÷" />
                    <Button val="×" />
                    
                    <Button val="7" />
                    <Button val="8" />
                    <Button val="9" />
                    <Button val="-" />
                    
                    <Button val="4" />
                    <Button val="5" />
                    <Button val="6" />
                    <Button val="+" />

                    <Button val="1" />
                    <Button val="2" />
                    <Button val="3" />
                    <Button val="=" className="row-span-2" />

                    <Button val="0" className="col-span-2" />
                    <Button val="." />
                </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border max-w-2xl mx-auto">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title text-center">How a Simple Calculator Helps Your Baking</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p className="text-center">Even with specialized tools, a basic calculator is indispensable in the kitchen. Here are a few ways it can save you time and improve your accuracy without needing to switch apps.</p>
                    <ul className="list-disc pl-5 space-y-2">
                        <li>
                            <strong>Quick Costing:</strong> Sum up your ingredient costs on the fly. If flour is $0.50, sugar is $0.30, and eggs are $0.40, quickly add them up (0.50 + 0.30 + 0.40) to get a baseline cost for your recipe.
                        </li>
                        <li>
                            <strong>Simple Scaling:</strong> Need to make a half-batch of cookies that calls for 240g of flour? A quick calculation (240 ÷ 2) tells you you need 120g. Doubling a recipe that needs 75g of butter? Just calculate 75 × 2.
                        </li>
                        <li>
                            <strong>Temperature Conversion (Approximation):</strong> If you know the formula, you can quickly convert temperatures. To go from 180°C to Fahrenheit, calculate (180 × 9 ÷ 5) + 32 to get 356°F.
                        </li>
                        <li>
                            <strong>Time Management:</strong> If a dough needs to proof for 45 minutes and then bake for 25 minutes, you can easily calculate the total time (45 + 25 = 70 minutes) to plan your schedule.
                        </li>
                    </ul>
                    <p>While our dedicated <button onClick={() => setActiveTool('cake-pricing')} className="font-bold text-brand-pink hover:underline">Pricing</button> and <button onClick={() => setActiveTool('recipe-scaler')} className="font-bold text-brand-pink hover:underline">Scaling</button> tools are more powerful for complex tasks, this simple calculator is perfect for those quick, everyday kitchen calculations.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
