import React, { useState } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { BodyIcon, ClipboardIcon, CheckCircleIcon } from '../Icons';

interface BodyFatCalculatorProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

type UnitSystem = 'metric' | 'imperial';
type Gender = 'female' | 'male';

interface Results {
    bodyFatPercentage: number;
    fatMass: number;
    leanMass: number;
}

const getCategory = (gender: Gender, bfp: number): { category: string, color: string } => {
    if (gender === 'female') {
        if (bfp < 14) return { category: 'Essential Fat / Athletes', color: 'bg-blue-200' };
        if (bfp < 25) return { category: 'Fitness', color: 'bg-green-200' };
        if (bfp < 32) return { category: 'Average', color: 'bg-yellow-200' };
        return { category: 'Obese', color: 'bg-red-200' };
    } else { // male
        if (bfp < 6) return { category: 'Essential Fat', color: 'bg-blue-200' };
        if (bfp < 18) return { category: 'Athletes / Fitness', color: 'bg-green-200' };
        if (bfp < 25) return { category: 'Average', color: 'bg-yellow-200' };
        return { category: 'Obese', color: 'bg-red-200' };
    }
};

export const BodyFatCalculator: React.FC<BodyFatCalculatorProps> = ({ setActiveTool, breadcrumbs }) => {
    const [unitSystem, setUnitSystem] = useState<UnitSystem>('imperial');
    const [gender, setGender] = useState<Gender>('female');
    const [height, setHeight] = useState('65');
    const [heightCm, setHeightCm] = useState('165');
    const [neck, setNeck] = useState('13');
    const [neckCm, setNeckCm] = useState('33');
    const [waist, setWaist] = useState('28');
    const [waistCm, setWaistCm] = useState('71');
    const [hips, setHips] = useState('36');
    const [hipsCm, setHipsCm] = useState('91');
    const [weight, setWeight] = useState('132');
    const [weightKg, setWeightKg] = useState('60');
    const [results, setResults] = useState<Results | null>(null);
    const [copied, setCopied] = useState(false);

    const labelStyles = "text-sm font-semibold text-brand-text-body mb-2 block";
    const inputStylesDark = "w-full px-4 py-3 bg-brand-text-title text-gray-200 placeholder-gray-400 border-transparent rounded-lg focus:ring-2 focus:ring-brand-pink focus:outline-none transition-shadow";


    const handleCalculate = () => {
        let h_in, n_in, w_in, hips_in;
        if (unitSystem === 'metric') {
            h_in = parseFloat(heightCm) / 2.54;
            n_in = parseFloat(neckCm) / 2.54;
            w_in = parseFloat(waistCm) / 2.54;
            hips_in = parseFloat(hipsCm) / 2.54;
        } else {
            h_in = parseFloat(height);
            n_in = parseFloat(neck);
            w_in = parseFloat(waist);
            hips_in = parseFloat(hips);
        }

        const totalWeightLbs = unitSystem === 'metric' ? parseFloat(weightKg) * 2.20462 : parseFloat(weight);
        const totalWeightKg = unitSystem === 'metric' ? parseFloat(weightKg) : parseFloat(weight) * 0.453592;

        if (isNaN(h_in) || isNaN(n_in) || isNaN(w_in) || (gender === 'female' && isNaN(hips_in)) || isNaN(totalWeightLbs)) {
            alert("Please fill all fields with valid numbers.");
            return;
        }

        let bfp = 0;
        if (gender === 'male') {
            bfp = 86.010 * Math.log10(w_in - n_in) - 70.041 * Math.log10(h_in) + 36.76;
        } else {
            bfp = 163.205 * Math.log10(w_in + hips_in - n_in) - 97.684 * Math.log10(h_in) - 78.387;
        }

        const fatMassLbs = totalWeightLbs * (bfp / 100);
        const leanMassLbs = totalWeightLbs - fatMassLbs;
        const fatMassKg = totalWeightKg * (bfp / 100);
        const leanMassKg = totalWeightKg - fatMassKg;

        setResults({
            bodyFatPercentage: parseFloat(bfp.toFixed(1)),
            fatMass: unitSystem === 'imperial' ? parseFloat(fatMassLbs.toFixed(1)) : parseFloat(fatMassKg.toFixed(1)),
            leanMass: unitSystem === 'imperial' ? parseFloat(leanMassLbs.toFixed(1)) : parseFloat(leanMassKg.toFixed(1)),
        });
    };

    const handleCopy = async () => {
        if (!results) return;
        const text = `My Estimated Body Fat: ${results.bodyFatPercentage}%\nFat Mass: ${results.fatMass} ${unitSystem === 'metric' ? 'kg' : 'lbs'}\nLean Mass: ${results.leanMass} ${unitSystem === 'metric' ? 'kg' : 'lbs'}`;
        try {
            await navigator.clipboard.writeText(text);
            setCopied(true);
            setTimeout(() => setCopied(false), 2000);
        } catch (err) {
            alert('Failed to copy results.');
        }
    };

    return (
        <ToolContainer title="Free Body Fat Calculator" description="Estimate your body fat percentage for free using the U.S. Navy method, which requires only a tape measure." setActiveTool={setActiveTool} breadcrumbs={breadcrumbs}>
            <div className="grid md:grid-cols-2 gap-8">
                <div className="space-y-6">
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-4 gap-y-6">
                        <div>
                            <label className={labelStyles}>Unit System</label>
                            <div className="flex gap-1 bg-brand-pink-light p-1 rounded-lg">
                                <button onClick={() => setUnitSystem('imperial')} className={`flex-1 py-1.5 text-sm font-bold rounded-md transition-colors ${unitSystem === 'imperial' ? 'bg-white shadow-sm text-brand-pink' : 'text-brand-text-body/80'}`}>Imperial</button>
                                <button onClick={() => setUnitSystem('metric')} className={`flex-1 py-1.5 text-sm font-bold rounded-md transition-colors ${unitSystem === 'metric' ? 'bg-white shadow-sm text-brand-pink' : 'text-brand-text-body/80'}`}>Metric</button>
                            </div>
                        </div>
                         <div>
                            <label className={labelStyles}>Gender</label>
                            <select value={gender} onChange={e => setGender(e.target.value as Gender)} className="w-full px-3 py-2.5 bg-white border border-brand-border rounded-lg">
                                <option value="female">Female</option>
                                <option value="male">Male</option>
                            </select>
                        </div>
                         <div>
                            <label className={labelStyles}>Height ({unitSystem === 'imperial' ? 'in' : 'cm'})</label>
                            <input type="number" value={unitSystem === 'imperial' ? height : heightCm} onChange={e => unitSystem === 'imperial' ? setHeight(e.target.value) : setHeightCm(e.target.value)} className={inputStylesDark}/>
                        </div>
                        <div>
                            <label className={labelStyles}>Weight ({unitSystem === 'imperial' ? 'lbs' : 'kg'})</label>
                            <input type="number" value={unitSystem === 'imperial' ? weight : weightKg} onChange={e => unitSystem === 'imperial' ? setWeight(e.target.value) : setWeightKg(e.target.value)} className={inputStylesDark}/>
                        </div>
                        <div>
                            <label className={labelStyles}>Neck ({unitSystem === 'imperial' ? 'in' : 'cm'})</label>
                            <input type="number" value={unitSystem === 'imperial' ? neck : neckCm} onChange={e => unitSystem === 'imperial' ? setNeck(e.target.value) : setNeckCm(e.target.value)} className={inputStylesDark}/>
                        </div>
                        <div>
                            <label className={labelStyles}>Waist ({unitSystem === 'imperial' ? 'in' : 'cm'})</label>
                            <input type="number" value={unitSystem === 'imperial' ? waist : waistCm} onChange={e => unitSystem === 'imperial' ? setWaist(e.target.value) : setWaistCm(e.target.value)} className={inputStylesDark}/>
                        </div>
                        {gender === 'female' && (
                            <div>
                                <label className={labelStyles}>Hips ({unitSystem === 'imperial' ? 'in' : 'cm'})</label>
                                <input type="number" value={unitSystem === 'imperial' ? hips : hipsCm} onChange={e => unitSystem === 'imperial' ? setHips(e.target.value) : setHipsCm(e.target.value)} className={inputStylesDark}/>
                            </div>
                        )}
                    </div>
                     <button onClick={handleCalculate} className="w-full bg-brand-pink text-white font-bold py-4 rounded-lg shadow-lg hover:opacity-90 transition-all duration-300">
                        Calculate Body Fat
                    </button>
                </div>
                <div className="bg-brand-pink-light p-6 rounded-lg text-center">
                    {results ? (
                        <div className="fade-in">
                            <h3 className="font-semibold text-brand-text-body">Body Fat Percentage</h3>
                            <p className="font-serif font-bold text-7xl text-brand-pink my-2">{results.bodyFatPercentage}%</p>
                            <div className={`inline-block px-3 py-1 text-sm font-semibold rounded-full ${getCategory(gender, results.bodyFatPercentage).color}`}>
                                {getCategory(gender, results.bodyFatPercentage).category}
                            </div>
                            <div className="grid grid-cols-2 gap-4 mt-6 text-left bg-white p-4 rounded-lg border">
                                <div>
                                    <h4 className="font-bold text-brand-text-title">Fat Mass</h4>
                                    <p className="text-lg font-semibold text-brand-text-body">{results.fatMass} {unitSystem === 'metric' ? 'kg' : 'lbs'}</p>
                                </div>
                                <div>
                                    <h4 className="font-bold text-brand-text-title">Lean Mass</h4>
                                    <p className="text-lg font-semibold text-brand-text-body">{results.leanMass} {unitSystem === 'metric' ? 'kg' : 'lbs'}</p>
                                </div>
                            </div>
                             <div className="mt-4">
                                <button onClick={handleCopy} className="inline-flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline">
                                    {copied ? <CheckCircleIcon className="w-4 h-4" /> : <ClipboardIcon className="w-4 h-4" />}
                                    {copied ? 'Results Copied!' : 'Copy Results'}
                                </button>
                            </div>
                        </div>
                    ) : (
                        <div className="flex flex-col items-center justify-center h-full">
                            <BodyIcon className="w-12 h-12 text-brand-pink mb-4" />
                            <h3 className="text-xl font-bold text-brand-text-title">Your results will appear here</h3>
                            <p className="text-sm text-brand-text-body/80 mt-1">Fill out the form to get started.</p>
                        </div>
                    )}
                </div>
            </div>
            <div className="mt-8 pt-6 border-t border-brand-border">
                <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title">Understanding Your Results</h3>
                <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-4">
                    <p>This calculator provides an estimate of your body composition based on circumference measurements. Here's a quick guide:</p>
                    <ul className="list-disc pl-5">
                        <li><strong>Body Fat Percentage:</strong> The proportion of your total body weight that is fat.</li>
                        <li><strong>Fat Mass:</strong> The total weight of the fat in your body.</li>
                        <li><strong>Lean Mass:</strong> The weight of everything else—muscles, bones, organs, and water.</li>
                    </ul>
                     <p><strong>Disclaimer:</strong> The tape measure method is an estimation and can be affected by how tightly you pull the tape and where exactly you measure. For the most accurate results, consider methods like DEXA scans or hydrostatic weighing. Consult a healthcare professional for personalized health advice.</p>
                </div>
            </div>
        </ToolContainer>
    );
};
