import React, { useState, useCallback } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { ThermometerIcon } from '../Icons';

interface TemperatureConverterProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

const UnitInput: React.FC<{
    label: string;
    value: string;
    onFocus: () => void;
    onChange: (val: string) => void;
    isActive: boolean;
}> = ({ label, value, onFocus, onChange, isActive }) => (
    <div>
        <label className={`block text-lg font-semibold mb-2 transition-colors ${isActive ? 'text-brand-pink' : 'text-brand-text-title'}`}>{label}</label>
        <input 
            type="number" 
            value={value}
            onFocus={onFocus}
            onChange={e => onChange(e.target.value)}
            className={`w-full p-4 text-3xl font-bold bg-white border-2 rounded-lg text-center transition-all duration-200 ${isActive ? 'border-brand-pink ring-2 ring-brand-pink/50' : 'border-brand-border'}`}
        />
    </div>
);

export const TemperatureConverter: React.FC<TemperatureConverterProps> = ({ setActiveTool, breadcrumbs }) => {
    const [celsius, setCelsius] = useState('100');
    const [fahrenheit, setFahrenheit] = useState('212');
    const [kelvin, setKelvin] = useState('373.15');
    const [activeUnit, setActiveUnit] = useState<'C' | 'F' | 'K'>('C');

    const handleCelsiusChange = useCallback((value: string) => {
        const C = parseFloat(value);
        setCelsius(value);
        if (isNaN(C)) {
            setFahrenheit(''); setKelvin(''); return;
        }
        setFahrenheit(((C * 9/5) + 32).toFixed(2));
        setKelvin((C + 273.15).toFixed(2));
    }, []);

    const handleFahrenheitChange = useCallback((value: string) => {
        const F = parseFloat(value);
        setFahrenheit(value);
        if (isNaN(F)) {
            setCelsius(''); setKelvin(''); return;
        }
        const C = (F - 32) * 5/9;
        setCelsius(C.toFixed(2));
        setKelvin((C + 273.15).toFixed(2));
    }, []);

    const handleKelvinChange = useCallback((value: string) => {
        const K = parseFloat(value);
        setKelvin(value);
        if (isNaN(K)) {
            setCelsius(''); setFahrenheit(''); return;
        }
        const C = K - 273.15;
        setCelsius(C.toFixed(2));
        setFahrenheit(((C * 9/5) + 32).toFixed(2));
    }, []);

    return (
        <ToolContainer 
            title="Free Temperature Converter (°C, °F, K)" 
            description="Quickly convert temperatures between Celsius (°C), Fahrenheit (°F), and Kelvin (K) for all your cooking and baking needs." 
            setActiveTool={setActiveTool} 
            breadcrumbs={breadcrumbs}
        >
            <div className="max-w-4xl mx-auto">
                <div className="grid sm:grid-cols-3 gap-6">
                    <UnitInput 
                        label="Celsius (°C)" 
                        value={celsius} 
                        onFocus={() => setActiveUnit('C')}
                        onChange={handleCelsiusChange}
                        isActive={activeUnit === 'C'}
                    />
                    <UnitInput 
                        label="Fahrenheit (°F)" 
                        value={fahrenheit}
                        onFocus={() => setActiveUnit('F')}
                        onChange={handleFahrenheitChange}
                        isActive={activeUnit === 'F'}
                    />
                    <UnitInput 
                        label="Kelvin (K)" 
                        value={kelvin}
                        onFocus={() => setActiveUnit('K')}
                        onChange={handleKelvinChange}
                        isActive={activeUnit === 'K'}
                    />
                </div>
                <div className="mt-8 pt-8 border-t border-brand-border">
                    <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title text-center">Common Baking Temperatures</h3>
                    <div className="grid sm:grid-cols-3 gap-4 text-center">
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                            <h4 className="font-bold text-brand-text-title">175°C / 350°F</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">Standard for most cakes, cookies, and brownies.</p>
                        </div>
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                            <h4 className="font-bold text-brand-text-title">200°C / 400°F</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">Good for roasting or getting a golden-brown color on pastries.</p>
                        </div>
                        <div className="bg-white p-4 rounded-lg border border-brand-border">
                             <h4 className="font-bold text-brand-text-title">220°C / 425°F</h4>
                            <p className="text-sm text-brand-text-body/80 mt-1">Used for items needing a quick, hot bake like pizza or some artisan breads.</p>
                        </div>
                    </div>
                </div>
                <div className="mt-8 pt-8 border-t border-brand-border">
                    <h3 className="font-serif font-bold text-2xl mb-4 text-brand-text-title text-center">Temperature Conversion Formulas</h3>
                    <div className="prose max-w-none text-brand-text-body/90 leading-relaxed space-y-2 text-center">
                        <p><strong>Celsius to Fahrenheit:</strong> (°C × 9/5) + 32 = °F</p>
                        <p><strong>Fahrenheit to Celsius:</strong> (°F − 32) × 5/9 = °C</p>
                        <p><strong>Celsius to Kelvin:</strong> °C + 273.15 = K</p>
                        <p><strong>Kelvin to Celsius:</strong> K − 273.15 = °C</p>
                    </div>
                </div>
            </div>
        </ToolContainer>
    );
};
